import { Component, Inject, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import {DialogService} from '../dialog.service';
import { DOCUMENT } from '@angular/common';
import { CustomerAuthenticatedDialogComponent } from '../customer-authenticated-dialog/customer-authenticated-dialog.component';


@Component({
  selector: 'app-fingerprint-authentication',
  templateUrl: './fingerprint-authentication.component.html',
  styleUrls: ['./fingerprint-authentication.component.scss']
})
export class FingerprintAuthenticationComponent implements OnInit {

  titleMessage : string = "Please use customers fingerprint to authenticate";
  contentMessage : string = "Please place Customer's thumb on the fingerprint reader to proceed";


  constructor(
    private router: Router,
    public dialogRef: MatDialogRef<FingerprintAuthenticationComponent>,
    private dialogService : DialogService,
    public dialog: MatDialog,
    // @Inject(MAT_DIALOG_DATA) public data: any,
    @Inject(DOCUMENT) private _document: Document
  ) { }

  ngOnInit() {
  //  console.log('data:: ' ,this.data);

  }

  Close(){
    this.dialogRef.close('close');
    console.log('The dialog was closed');
  }

  Scan() {
    this.dialogRef.close();
    // this.successDialog();
  }
  successDialog(): void {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: '60%',
      height: '75%',
      backdropClass: 'dialog-bg-trans',
      // disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      // this._document.defaultView.location.reload();
    });
  }


}
