import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TokenService } from 'app/shared/services/token-service/token.service';

@Component({
  selector: 'app-verification-completed',
  templateUrl: './verification-completed.component.html',
  styleUrls: ['./verification-completed.component.scss']
})
export class VerificationCompletedComponent implements OnInit {
  customerId:any;
  constructor(private tokenService: TokenService,public router:Router,public dialogRef: MatDialogRef<VerificationCompletedComponent>) { }
  closeClick(){
    this.dialogRef.close();
  }
  ngOnInit(): void {
    this.tokenService.getId().subscribe(info => {
     
     this.assign(info.id)
    })
  }
  assign(id){
    this.customerId = id;
  }
done(){
  this.dialogRef.close();
  this.router.navigateByUrl('/others/dashboard');
}
}
