import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';


@Component({
  selector: 'app-customer-authenticated-dialog',
  templateUrl: './customer-authenticated-dialog.component.html',
  styleUrls: ['./customer-authenticated-dialog.component.scss']
})
export class CustomerAuthenticatedDialogComponent implements OnInit {

  titleMessage: string = "Customer Authenticated!";
  contentMessage: string = "Transaction saved successfully!";

  constructor(
    private router: Router,
    public dialogRef: MatDialogRef<CustomerAuthenticatedDialogComponent>
  ) { }

  ngOnInit() {
    
  }

  Close(): void {
    this.dialogRef.close();
    this.router.navigate(['/others/dashboard']);
  }


}
