import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef,MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
interface withdrawalAlert{
  tokenNumber:number
}

@Component({
  selector: 'app-withdrawl-alert-dialogue',
  templateUrl: './withdrawl-alert-dialogue.component.html',
  styleUrls: ['./withdrawl-alert-dialogue.component.scss']
})
export class WithdrawlAlertDialogueComponent implements OnInit {
  payload: withdrawalAlert;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<WithdrawlAlertDialogueComponent>,
    public api: ApiService
  ) { }

  ngOnInit(): void {
    this.payload = this.data.payload;
  }

  Yes(): void {
    this.dialogRef.close("Yes");
  }

  cancelOperation() {
    this.api.updateTokenStatus(this.payload).subscribe(resp=>{
    });
    this.dialogRef.close("Yes");
  }
}
