import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatTableDataSource } from '@angular/material/table';
import { ApiService } from 'app/shared/services/api.service';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { DashboardComponent } from '../../dashboard/dashboard.component';
import { CustomerAuthenticatedDialogComponent } from '../../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component';
import { DialogService } from '../../Dialogs/dialog.service';
import { FingerprintAuthenticationComponent } from '../../Dialogs/fingerprint-authentication/fingerprint-authentication.component';
import { OperationsComponent } from '../../operations/operations.component';

@Component({
  selector: 'app-new-forex-transaction',
  templateUrl: './new-forex-transaction.component.html',
  styleUrls: ['./new-forex-transaction.component.scss']
})
export class NewForexTransactionComponent implements OnInit {
  forexTransactionFormNew: FormGroup;
  countryValues: any;
  transValue: number;
  totalTransactionAmount: number = 0;
  disableSave: boolean = true;
  accountBal: number = 0;
  chargeDetailArray: any;
  totalChargeAmount: any;
  exchangeRate: any;
  currencyImage: string;
  dataSource: any;
  loading: boolean;
  holderName: any;
  invalidAccNo: boolean;
  invalidAccNoMsg: string;
  accountResp: any;
  branchName: any;
  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];
  validPattern = "^(?![a-zA-Z\s]+$)[a-zA-Z0-9]+$"
  forexTranasctionData: any;
  estimatedTime: any;
  transactionId: any;
  branchNameFromLogin: string;
  tellerId: string;
  constructor(
    private operationsComponent: OperationsComponent,
    private dialogService: DialogService,
    private formBuilder: FormBuilder,
    private api: TokenService,
    private apiService: ApiService,
    private snack: MatSnackBar,
    private dashboard:DashboardComponent,
    private dialog: MatDialog


  ) { }

  ngOnInit(): void {
    this.branchNameFromLogin=localStorage.getItem('branchName');
    this.tellerId = localStorage.getItem('tellerid');
    this.getCountrynameValues();
this.buildForexTxnForm();

  }
  buildForexTxnForm() {
    this.forexTransactionFormNew = this.formBuilder.group({
      accountNumber: ['',[Validators.required,Validators.minLength(4),Validators.maxLength(15),Validators.pattern(this.validPattern)]],
      accountBalance: ['', [Validators.required]],
      holderName: ['', [Validators.required]],
      accountCurrency: ['', [Validators.required]],
      accountBranch: ['', [Validators.required]],
      fxCurrency: ['', [Validators.required]],
      fxAmount: ['', [Validators.required, Validators.min(.1)]],
      productCode: ['FTX'],
      tokenOrigin: ['Teller'],
      customerId: [''],
      transactionBranch: [''],
      tellerId: [''],
      transactionId: [''],
      transactionType:['',[Validators.required]],
      exchangeRate: [''],
      totalChargeAmount: [''],
      totalTransactionAmount: [''],
    })
  }
  fetchingHolderDetailsForCashWithdrwal(accountnum) {
    this.getChargeDetails()
    this.currencyImage = null
    this.loading = true;
    this.api.fetchingDetailsForCash(accountnum).subscribe(
      (resp) => {
        console.log(resp);
        this.loading = false;
        if (resp == null) {
          this.snack.open("Invalid Account number: " + accountnum, "", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
        if (resp.accountStatus && resp.accountStatus != "APPROVED") {
          this.snack.open("Account Number is not Approved :" + accountnum, "", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
          this.forexTransactionFormNew.reset();
          this.getCountrynameValues();
          return;
        }
        if (resp) {
          this.accountResp = resp;
          this.accountBal = resp.accBalance;
          this.forexTransactionFormNew.get('accountBalance').setValue(resp.accBalance);
          this.holderName = resp.accHolderName;
          this.forexTransactionFormNew.get('holderName').setValue(resp.holderName);
          this.invalidAccNo = false;
          this.invalidAccNoMsg = resp.message;
          if (this.invalidAccNoMsg == "No value present") {
            this.invalidAccNo = true;
          }
          this.forexTransactionFormNew
            .get("holderName")
            .patchValue(resp.accHolderName);
          this.forexTransactionFormNew
            .get("accountCurrency")
            .patchValue(resp.currency);
          this.forexTransactionFormNew
            .get("transactionBranch")
            .setValue(this.branchName);
          this.forexTransactionFormNew
            .get("accountBranch")
            .patchValue(resp.accountBranch);
        } else {
          this.invalidAccNo = true;
          console.log(this.invalidAccNo);
          this.snack.open("Incorrect Account Number", "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
      },
      (err) => {
        this.loading = false;
        this.snack.open(err.error.text, "", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.invalidAccNo = true;
        this.accountBal = null;
        this.holderName = "";
        this.forexTransactionFormNew.reset();
      }
    );
  }
  getCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValues: any) => {
      this.countryValues = allCountryValues;
    });
  }
  dataByCountryCode(index, item) {
    return item;
  }
  addCurrencyImage(currency) {
    return currency?.toLowerCase();
  }
  onCountryChange(country) {
    if (country.currencyCode != this.forexTransactionFormNew.get('accountCurrency').value) {
      this.forexTransactionFormNew.get('exchangeRate').setValue('');
      this.calculateExchangeRate(this.forexTransactionFormNew.get('transactionType').value);
    }
    else {
      this.forexTransactionFormNew.get('exchangeRate').setValue('');
      this.calculateTotalTransactionAmount();
    }
    this.disableSave = false;
    let currency = country.currencyCode;
    this.currencyImage = "currency-flag-" + currency.toLowerCase();
    console.log(this.forexTransactionFormNew);
    
  }
  Onlynumber(event): boolean {
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }
  _keyUp(){
    if (this.forexTransactionFormNew.get('accountNumber').value == "") {
     this.forexTransactionFormNew.reset();
      this.accountBal = null;
      this.holderName ="";
      this.totalTransactionAmount = 0;
      this.dataSource = null;
  }
  }
  getChargeDetails() {
    this.api.getChargeDetails(this.forexTransactionFormNew.value.accountNumber).subscribe((resp) => {
      this.chargeDetailArray = resp;
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
      this.calculateTotalCharge();
      if(this.forexTransactionFormNew.get('fxAmount').value){
        this.calculateTotalTransactionAmount();
      }
    });
  }
  calculateTotalCharge() {
    this.totalChargeAmount = 0;
    if (this.chargeDetailArray) {
      for (let i = 0; i < this.chargeDetailArray.length; i++) {
        if (this.chargeDetailArray[i].waiver != true && this.chargeDetailArray[i].amount != null) {
          this.totalChargeAmount = this.totalChargeAmount + this.chargeDetailArray[i].amount;
        }
      }
    }
    this.forexTransactionFormNew.get('totalChargeAmount').setValue(this.totalChargeAmount)
  }
  waiverChange(val: boolean, i: number) {
    this.chargeDetailArray[i].waiver = val;
    this.calculateTotalCharge();
    this.calculateTotalTransactionAmount();
  }
  calculateTotalTransactionAmount() {
    this.transValue = 0
    if (this.forexTransactionFormNew.get('exchangeRate').value) {
      this.transValue = Number(this.forexTransactionFormNew.get('fxAmount').value) * Number(this.forexTransactionFormNew.get('exchangeRate').value)
      this.totalTransactionAmount = (Number(this.forexTransactionFormNew.get('fxAmount').value) * Number(this.forexTransactionFormNew.get('exchangeRate').value)) + (Number(this.totalChargeAmount));
      this.forexTransactionFormNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount);
    }
    else {
      this.transValue = this.forexTransactionFormNew.get('fxAmount').value
      this.totalTransactionAmount = Number(this.forexTransactionFormNew.get('fxAmount').value) + Number(this.totalChargeAmount);
      this.forexTransactionFormNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount)
    }
  }
  onForexTransaction(){
    {
      let custId=this.accountResp.customerId;
      console.log('custId :: ',custId);
      this.forexTransactionFormNew.get('customerId').setValue(+custId);
      this.forexTransactionFormNew.get('tellerId').setValue(this.tellerId);
      this.forexTransactionFormNew.value.fxAmount=this.forexTransactionFormNew.get('fxAmount').value.replace(/\,/g,'');;
      this.forexTransactionFormNew.value.productCode = 'FTX';
      this.forexTransactionFormNew.value.tokenOrigin = 'Teller'
      const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
        width: '60%',
        height: '70%',
        backdropClass: 'dialog-bg-trans',
      });
  
      dialogRef.afterClosed().subscribe(result => {
        if(result != 'close'){
      this.api.saveofNewCashDeposit(this.forexTransactionFormNew.value).subscribe(data=>{
        if(data)
        {
          this.forexTransactionFormNew.value.transactionId = data.transactionId;
          this.transactionId = data.transactionId;
          this.accountBal = null;
          this.holderName ="";
          this.forexTranasctionData = data;
          this.forexTranasctionData.transactionType = 'Forex Transaction'
          this.estimatedTime = this.forexTranasctionData.estimatedWaitTime;
          localStorage.setItem("estimatedTiming", this.estimatedTime);
          if (this.forexTranasctionData) {
            this.successDialog();
            this.operationsComponent.closeDrawer();
            this.dashboard.getCounterDashBoard();
            this.dashboard.getDashboardRecords();
          }
  
         this.forexTransactionFormNew.reset();
         this.currencyImage = null
        }
      },(err)=>{
        this.snack.open(`Internal Server Error`, 'OK', {
          duration: 2000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      })
    }
    })
  }
}
  cancelOperation() {
    this.operationsComponent.closeDrawer();
    this.dashboard.serviceEnable=false;
  }
  successDialog(): void {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: '60%',
      height: '75%',
      backdropClass: 'dialog-bg-trans',
    });
  
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.showTransactionSlip();
    });
  }
   showTransactionSlip() {
      this.dashboard.slipEnable = true;
      this.dashboard.transactionDetails = this.forexTranasctionData
      this.dashboard.drawer.open()
    }
calculateExchangeRate(event){
  if(event && this.forexTransactionFormNew.get('fxCurrency').value && this.forexTransactionFormNew.get('accountCurrency').value){
    this.apiService.getExchangeRateforCurrency(this.forexTransactionFormNew.get('fxCurrency').value, this.forexTransactionFormNew.get('accountCurrency').value).subscribe(res => {
      if(event == 'buy'){
        this.exchangeRate = res[res.length-1].buyRate;
        this.forexTransactionFormNew.get('exchangeRate').setValue(res[res.length-1].buyRate)
      }
      else{
        this.exchangeRate = res[res.length-1].sellRate
        this.forexTransactionFormNew.get('exchangeRate').setValue(res[res.length-1].sellRate)
      }
      this.calculateTotalTransactionAmount();
    })
  }
 }
}
