import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { CustomerAuthenticatedDialogComponent } from "../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component";
import { DialogService } from "../Dialogs/dialog.service";
import { FingerprintAuthenticationComponent } from "../Dialogs/fingerprint-authentication/fingerprint-authentication.component";

@Component({
  selector: "app-operations",
  templateUrl: "./operations.component.html",
  styleUrls: ["./operations.component.scss"],
})
export class OperationsComponent implements OnInit {
  @Input() drawer;
  @Input("operationData") operationData: any;
  @Input("newcashoperation") newcashoperation: any;
  @Output() afterSubmit: EventEmitter<boolean> = new EventEmitter();

  cashDepositData: Object;
  chequeDepositData: Object;
  cashWithdrawalData: Object;
  forexTransactionData: Object;
  chequeWithdrawalData: Object;
  loanPaymentData: Object;
  depositTopUpData: Object;
  utilityBillPaymentData: Object;
  cashWithdrawalScreenEnable: boolean = false;
  cashDepositScreenEnable: boolean = false;
  forexTransactionScreenEnable: boolean = false;
  loanPaymentScreenEnable: boolean = false;
  utilityBillPaymentScreenEnable: boolean = false;
  chequeDepositScreenEnable: boolean = false;
  chequeWithdrawalScreenEnable: boolean = false;
  depositTopUpScreenEnable: boolean = false;
  newCashWithdrwal: boolean = false;
  newCashDeposit: boolean = false;
  newChequeDeposit: boolean;
  newChequeWithdrawal: boolean;
  newForexTransaction: boolean;

  constructor(private dialogService: DialogService, public dialog: MatDialog) {}

  ngOnChanges() {
    this.operationType();
  }

  ngOnInit() {
    this.operationType();
  }

  operationType() {
    
    this.resetingforms();
      if (this.operationData.trnType == "Cash Withdrawal") {
        this.cashWithdrawalScreenEnable = true;
        this.cashWithdrawalData = this.operationData;
        return;
      } else if (this.operationData.trnType == "Cash Deposit") {
        this.cashDepositScreenEnable = true;
        this.cashDepositData = this.operationData;
        return;
      } else if (this.operationData.trnType == "forex_transaction") {
        this.forexTransactionScreenEnable = true;
        this.forexTransactionData = this.operationData;
      } else if (this.operationData.trnType == "Cheque Withdrawal") {
        this.chequeWithdrawalScreenEnable = true;
        this.chequeWithdrawalData = this.operationData;
        return;
      } else if (this.operationData.trnType == "deposit_top_up") {
        this.depositTopUpScreenEnable = true;
        this.depositTopUpData = this.operationData;
      } else if (this.operationData.trnType == "loan_payment") {
        this.loanPaymentScreenEnable = true;
        this.loanPaymentData = this.operationData;
      } else if (this.operationData.trnType == "utility_bill_payment") {
        this.resetingservices();
        this.utilityBillPaymentScreenEnable = true;
        this.utilityBillPaymentData = this.operationData;
      } else if (this.operationData.trnType == "Cheque Deposit") {
        this.chequeDepositScreenEnable = true;
        this.chequeDepositData = this.operationData;
        return;
      } else if (this.operationData.trnType == "new Cash Withdrwal") {
        this.newCashWithdrwal = true;
        this.newCashDeposit = false;
        this.newChequeDeposit = false;
        this.newChequeWithdrawal = false;
        this.newForexTransaction = false;
      } else if (this.operationData.trnType == "new Cash Deposit") {
        this.newCashWithdrwal = false;
        this.newCashDeposit = true;
        this.newChequeDeposit = false;
        this.newChequeWithdrawal = false;
        this.newForexTransaction = false;
      } else if (this.operationData.trnType == "new Cheque Deposit") {
        this.newCashWithdrwal = false;
        this.newCashDeposit = false;
        this.newChequeDeposit = true;
        this.newChequeWithdrawal = false;
        this.newForexTransaction = false;
      } else if (this.operationData.trnType == "new Cheque Withdrawal") {
        this.newCashWithdrwal = false;
        this.newCashDeposit = false;
        this.newChequeDeposit = false;
        this.newChequeWithdrawal = true;
        this.newForexTransaction = false;
      } else if(this.operationData.trnType == 'new Forex Transaction'){
        this.newCashWithdrwal = false;
        this.newCashDeposit = false;
        this.newChequeWithdrawal = false;
        this.newChequeDeposit = false;
        this.newForexTransaction = true;
    }
      else {
      }
   
  }

  resetingforms() {
    this.cashWithdrawalScreenEnable = false;
    this.cashDepositScreenEnable = false;
    this.forexTransactionScreenEnable = false;
    this.chequeWithdrawalScreenEnable = false;
    this.depositTopUpScreenEnable = false;
    this.loanPaymentScreenEnable = false;
    this.utilityBillPaymentScreenEnable = false;
    this.chequeDepositScreenEnable = false;
  }

  resetingservices() {
    this.newCashWithdrwal = false;
    this.newCashDeposit = false;
    this.newChequeDeposit = false;
    this.newChequeWithdrawal = false;
    this.newForexTransaction = false;
  }

  closeDrawerAfterSuccess() {
    // this.drawer.close();
    this.closeDrawer();
    // this.fingerprintDialog();
    this.fingerprintDialog();
    this.afterSubmit.emit(true);
  }
  cashWithdrawl() {
    // this.drawer.close();
    this.closeDrawer();
    this.fingerprintDialog();
    // this.cashWithdrawal();
    this.afterSubmit.emit(true);
  }
  checashdipandchewithdrawl() {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: "60%",
      height: "75%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {});
  }

  closeDrawer() {
    this.drawer.close();
    this.cashWithdrawalScreenEnable = false;
    this.cashDepositScreenEnable = false;
    this.forexTransactionScreenEnable = false;
    this.loanPaymentScreenEnable = false;
    this.utilityBillPaymentScreenEnable = false;
    this.chequeWithdrawalScreenEnable = false;
    this.chequeDepositScreenEnable = false;
    this.depositTopUpScreenEnable = false;
    this.newCashWithdrwal = false;
    this.newCashDeposit = false;
    this.newChequeDeposit = false;
    this.newChequeWithdrawal = false;
    this.newForexTransaction = false;
  }

  fingerprintDialog(): void {
    const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
      width: "60%",
      height: "70%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe((result) => {});
  }
}
