import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Inputs } from 'app/shared/components/input/input';
import { InputComponent } from 'app/shared/components/input/input.component';

@Component({
  selector: 'app-reusablecomponent',
  templateUrl: './reusablecomponent.component.html',
  styleUrls: ['./reusablecomponent.component.scss']
})
export class ReusablecomponentComponent implements OnInit {
  inputtype:string="number";
  
  @ViewChild('input1') input1:InputComponent;
  inputdefaultvalue1:string;
  inputdefaultvalue: any;
  inputLabel:string="input method";
  ipValue:string;
  inputtypeTwo= "text";
  inputtype1: string='text';
  form: FormGroup;
  inputLabel1: string='Liab No';
  inputLabel2:string='Postel No';
  inptValue: number;
  inputs=new Inputs();
  inputError:string='Postal Code is required!';
  myGroup: FormGroup;
  
  
  constructor(private _formBuilder: FormBuilder) {
    this.myGroup = new FormGroup({
      postalCode: new FormControl()
    });
   }

  ngOnInit(): void {
   
    this.form = this._formBuilder.group({
      company   : [
          {
              value   : 'Google',
              disabled: true
          }, Validators.required
      ],
     
      postalCode: ['', [Validators.required, Validators.maxLength(6)]]
    });
   
}

addInput(){
    this.inputs.type="text";
 }
 addInput1(value){
 this.inptValue = value;
  this.inputs.type="number";

}








}
