import { Component, Inject, OnInit, ChangeDetectorRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TokenInformation } from 'app/shared/models/tokenInformation';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { AppLoaderService } from 'app/shared/services/app-loader/app-loader.service';
// import * as html2pdf from 'html2pdf.js';


@Component({
  selector: 'app-transaction-detail',
  templateUrl: './transaction-detail.component.html',
  styleUrls: ['./transaction-detail.component.scss']
})
export class TransactionDetailComponent implements OnInit {

  displayedColumns: string[] = ['waiver', 'chargeCode', 'chargeAmount'];
  displayedColumnForDenom: string[] = ['denomCode', 'denomValue', 'currency'];
  chargeDetails: any[] = [];
  tokenInfo: TokenInformation;
  title: string = '';
  titleArray: any[] = [
    { titleRef: 'cash_withdrawal', title: 'Cash Withdrawal' },
    { titleRef: 'cash_deposit', title: 'Cash Deposit' },
    { titleRef: 'forex_transaction', title: 'Forex Transaction' },
    { titleRef: 'cheque_withdrawal', title: 'Cheque Withdrawal' },
    { titleRef: 'deposit_top_up', title: 'Deposit Top Up' },
    { titleRef: 'loan_payment', title: 'Loan Payment' },
    { titleRef: 'utility_bill_payment', title: 'Utility Bill Payment' },
    { titleRef: 'cheque_deposit', title: 'Cheque Deposit' }
  ];
  revertStatus: any;
  revertBtn: boolean = false;
  screenName: any;
  transactionStatus: string;
  transactionInfo: boolean =false;
  tellerId: string;
  defaultImage = "./assets/images/face-7.jpg";
  approvedStatus: string;
  approveBtn: boolean = false;
  profileData: any;
  denomRequired: any;
  

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<TransactionDetailComponent>,
    private snack: MatSnackBar,
    private cdr: ChangeDetectorRef,
    private tokenService: TokenService,
    private loader:AppLoaderService
  ) { }

  ngOnInit(): void {
    console.log(this.data);
    this.screenName = this.data.screen;
    this.getTokenInformation(this.data.dataKey);
    this.tellerId =localStorage.getItem('tellerid');
    this.denomRequired = localStorage.getItem('denomRequired');
  }

  getTokenInformation(tokenId) {
    this.loader.open();
    this.tokenService.getOperationData(tokenId).subscribe((resp) => {
      console.log("resp ::",resp)
      this.loader.close();
      this.tokenInfo = resp;
      this.profileData=resp;
      console.log(this.tokenInfo);
      if (resp.revertTransTime) {
        console.log('if')
        this.revertStatus = "Revert Transaction Requested on : "+resp.revertTransTime;
        if(resp.revertTransactionApprovedTime){
          this.approveBtn = true;
          this.approvedStatus = "Revert Transaction Approved on : "+resp.revertTransactionApprovedTime;
        } else {
          this.approveBtn = false;
        }
        this.revertBtn = true;
      } else {
        this.revertBtn = false;
      }

      if (this.screenName === 'dashboard') {
        if (resp['transactionStatus'] === 'REJECTED' || resp['transactionStatus'] === 'APPROVED' || resp['transactionStatus'] === 'REVERT') {
          this.transactionStatus= "Revert Transaction "+ resp['transactionStatus'] ;
          this.transactionInfo = false;
        }else{
          this.transactionInfo = true;
        }
      }
      console.log(" this.transactionInfo::", this.transactionInfo)
      this.setTitle(this.tokenInfo.trnType);
      this.tokenService.getChargeDetails(resp.accountNumber).subscribe((response) => {
        this.chargeDetails = response;
        console.log(this.chargeDetails);
        // this.dataSource = new MatTableDataSource(this.chargeDetails);
      });
    });
  }

  setTitle(titleRef) {
    for (let i = 0; i < this.titleArray.length; i++) {
      if (this.titleArray[i].titleRef == titleRef) {
        this.title = this.titleArray[i].title;
      }
    }
  }
  printPdf(){

     //var element=document.getElementById("recenttxn");
    // var docBackup=document.body.innerHTML;
    // document.body.innerHTML=element;
    // window.print();
    // document.body.innerHTML=docBackup;

    var element = document.getElementById("recenttxn").innerHTML;
    var docBackup = window.open('Rumango','','left=0,top=0,width=900,height=900,toolbar=0,scrollbars=0,status=0');    
    docBackup.document.write(` <html>
        <head>
          <title>Rumango</title> 
          <style>
         img{  
          margin-top:15px;
          width: 80px;
    height: 80px;
    border-radius: 50%;}
          </style>         
        </head>
    <body onload="window.print();window.close()">${element}</body>
      </html>`);
    docBackup.document.close();
 

// var opt={
//   margin:[10,10,10,10],
//   jspdf:{unit:'in', format:'letter' , orientation:'portrait'}
// }
//     html2pdf().set(opt).from(element).save();
  }

  goToClose(): void {
    var data = '';
    if (this.data.screen === 'dashboard') {
      data = 'no data';
    }

    if (this.data.screen === 'summary') {
      data = 'no data';
    }
    this.dialogRef.close({
      message: 'Confirm',
      // screen: this.data.screen
    });
    
  }

  revertTransaction(tokenInfo) {
    console.log('revert transaction :: ', tokenInfo);
    tokenInfo.transactionStatus = 'REVERT';
    tokenInfo.revertTransaction = true;
    this.tokenService.updateRequestForRevertTransaction(tokenInfo)
      .subscribe(updateResp => {
        console.log("revert trxn updateResp :: ", updateResp);
        if (updateResp.hasOwnProperty('responseMessage')) {
          if (updateResp['responseMessage'] === 'Reverted') {
            this.cdr.detectChanges();
            this.cdr.markForCheck();
            this.revertStatus = "Revert Transaction Requested"
            this.revertBtn = true;
          } else {
            this.cdr.detectChanges();
            this.cdr.markForCheck();
            this.revertBtn = false;

          }
        } else {
          this.cdr.detectChanges();
          this.cdr.markForCheck();
          this.revertStatus = "Revert Transaction Requested"
          this.revertBtn = true;
          this.snack.open(`Request Sent Successfully`, 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          var data = updateResp;
          this.cdr.detectChanges();
          this.cdr.markForCheck();
          this.dialogRef.close({
            message: 'Confirm',
            data
          });
        }
      }, error => {
        console.log("revert trxn error response :: ", error)
      })
  }

  approveOrReject(tokenInfo, status) {
    tokenInfo.transactionStatus = status;
    tokenInfo.revertTransaction = false;
    tokenInfo.tellerId = this.tellerId;
    console.log('approve/reject transaction :: ', tokenInfo,tokenInfo.tellerID);

    if (status === 'APPROVED') {
      tokenInfo.accountBalance = tokenInfo.accountBalance + tokenInfo.transactionAmount;
      tokenInfo.finalTransactionAmount = tokenInfo.finalTransactionAmount - tokenInfo.transactionAmount;
      tokenInfo.totalAmount = 0;
      tokenInfo.transactionAmount = 0;
    }

    this.tokenService.approveOrRejectRequestForRevertTransaction(tokenInfo)
      .subscribe(updateResp => {
        console.log("Approve/Reject trxn updateResp :: ", updateResp);
        if (updateResp.hasOwnProperty('responseMessage')) {
          if (updateResp['responseMessage'] === 'No Revert Request') {
            // this.revertStatus="Revert Transaction Requested"
            this.revertBtn = false;
            this.cdr.detectChanges();
            this.cdr.markForCheck();
          } else {
            this.revertBtn = false;
          }
        } else {
          var data = updateResp;
          this.cdr.detectChanges();
          this.cdr.markForCheck();
          this.revertBtn = false;
          this.dialogRef.close({
            message: 'Confirm',
            data
          });
          this.snack.open(`Successfully ${status}!`, 'OK', {
            duration: 4000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
        }
      }, error => {
        console.log("revert trxn error response :: ", error)
      })
  }

}
