import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatAccordion } from '@angular/material/expansion';
import { MatTableDataSource } from '@angular/material/table';
import { UtilityBillPayment } from 'app/shared/models/utilityBillPayment';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { forkJoin } from 'rxjs';
import { OperationsComponent } from '../../operations/operations.component';
import { FingerPrintVerificationComponent } from '../finger-print-verification/finger-print-verification.component';

@Component({
  selector: 'app-utility-bill-payment',
  templateUrl: './utility-bill-payment.component.html',
  styleUrls: ['./utility-bill-payment.component.scss']
})


export class UtilityBillPaymentComponent implements OnInit {
  @ViewChild('upForm') upForm: NgForm;
  @ViewChild(MatAccordion) accordion: MatAccordion;

  utilityBillPayment:UtilityBillPayment;
  @Input("utilityBillPaymentData") utilityBillPaymentData: any;

  totalAmt: Number;
 
  title: string;
  
  dataOfBillerName: any;
  operation: string;
  detailsPanelOpened: boolean = false;
  chargeDetailsPanelOpened: boolean = false;
  denominationPanelOpened: boolean = false;
  chargeDetailArray : any[] = [];
  dataSource : any;
  accountTypeId : number;
  pauseTrans: boolean=false;
  constructor(private tokenService: TokenService,public dialog: MatDialog,private operationsComponent : OperationsComponent) { }

  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];

  ngOnInit(): void {
   
    this.utilityBillPayment = this.utilityBillPaymentData ;
   
    this.tokenService.getBillerName().subscribe((result) => {
     this.dataOfBillerName=result;
     this.getChargeDetails();
    },
      (error) => {
        console.error(error);
      }
    );
  }
  ngOnChanges() {
    this.utilityBillPayment = this.utilityBillPaymentData;
  }
  pauseTransaction(){
    this.pauseTrans = !this.pauseTrans;
  }
  getChargeDetails(){
    if(this.utilityBillPayment.accountType == "Savings"){
      this.accountTypeId = 1;
    }
    else if(this.utilityBillPayment.accountType == "Current"){
      this.accountTypeId = 2;
    }
    else if(this.utilityBillPayment.accountType == "Joint"){
      this.accountTypeId = 3;
    }
    else{
      this.accountTypeId = null;
    }
    this.tokenService.getChargeDetails(this.utilityBillPayment.accountNumber).subscribe((resp) => {
      console.log(resp);
      this.chargeDetailArray = resp;
      console.log(this.chargeDetailArray);
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
    });
  }

  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }
  OnSelectingBillName(billName){
 
    this.tokenService.getUtilityBillPayment(billName).subscribe((result) => {

    if(result){
        this.utilityBillPayment=result;

      }

    },
      (error) => {
        console.error(error);
      }
    );

  }

  waiverChange(val: boolean, i: number) {
    this.chargeDetailArray[i].waiver = val;
    console.log(this.chargeDetailArray);
  }


  onclickofConfirm(upForm: NgForm) {
    
    this.utilityBillPayment = upForm.value;
    if(this.pauseTrans==false){
    forkJoin(
      this.tokenService.saveUtilityBillPayment(this.utilityBillPayment),
      this.tokenService.saveChargeDetails(this.chargeDetailArray)
    ).subscribe((res) => {
      if (res[0] && res[1]) {
        console.log(res[0]);
        console.log(res[1]);
        this.upForm.reset();
        this.accordion.closeAll();
        this.operationsComponent.closeDrawerAfterSuccess();
      }
    },
      (error) => {
        console.error(error);
      }
    );
    }

  }

  cancelOperation(){
    this.upForm.reset();
    this.accordion.closeAll();
    this.operationsComponent.closeDrawer();
  }

  openDialog(): void {
    var name='cat';
     let dialogRef = this.dialog.open(FingerPrintVerificationComponent, {
       width: '500px',
       height:'500px',
       data: { name: name }
     });
 
     dialogRef.afterClosed().subscribe(result => {
       console.log('The dialog was closed');
       name = result;
     });
   }

}
