  getval(userData: any) {
    if (userData != null && userData != undefined && userData != '') {
      for (let i = 0; i < this.checkData.length; i++) {
        if (this.checkData[i].user == userData) {
          this.checkDataCount++;
          let newEntry = {
            user: userData,
            bot: '',
            userTextTime: new Date().toLocaleTimeString(),
            botTextTime: '',
            cusId: 0,
          };
          this.chatData.push(newEntry);
          setTimeout(() => {
            this.chatData[this.countValue].bot = this.checkData[i].bot;
            (this.chatData[
              this.countValue
            ].botTextTime = new Date().toLocaleTimeString()),
              this.countValue++;
          }, 2000);
        }
      }
      if (this.checkDataCount == 0) {
        let key: string = '';
        if (!isNaN(userData)) {
          this.tokenService
            .getCustomerByCustomerID('accountId', userData)
            .subscribe((resp) => {
              if (resp) {
                this.getCustomerRecords('accountId', userData);
              } else {
                this.getCustomerRecords('customerId', userData);
              }
            });
        } else if (userData.substring(0, 3) == 'KYC') {
          this.getCustomerRecords('kycReference', userData);
        } else {
          this.getCustomerRecords('customerName', userData);
        }
        let newEntry = {
          user: userData,
          bot: '',
          userTextTime: new Date().toLocaleTimeString(),
          botTextTime: '',
          cusId: 0,
        };
        this.chatData.push(newEntry);
      }
      this.checkDataCount = 0;
    } else {
    }
    this.inputvalue = '';
    this.disablevalue = true;
  }

