  convertDdMmToMmDD(issuedTime) {
    const [date, month] = issuedTime.split("-");
    const issue1Date = new Date(issuedTime);
    issue1Date.setDate(date);
    issue1Date.setMonth(month-1);
    return issue1Date;
  }

  sortGroupByIssueDate(customerGroup) {
    return customerGroup.sort((customer1, customer2) => {
      const issue1Date = this.convertDdMmToMmDD(customer1.issuedTime);
      const issue2Date = this.convertDdMmToMmDD(customer1.issuedTime);

      if (issue1Date < issue2Date)
        return -1;
      return 1;
    });
  };
  
  groupCustomersByCategory(customers) {
    const groupedCustomers = {
      S: [],
      P: [],
      N: []
    };
  
    customers.forEach((customer) => {
      const { customerCategory } = customer;
      if (!groupedCustomers[customerCategory])
        groupedCustomers[customerCategory] = [];
      groupedCustomers[customerCategory].push(customer);
    });
  
    Object.keys(groupedCustomers).forEach((key) => {
      const value = groupedCustomers[key];
      groupedCustomers[key] = this.sortGroupByIssueDate(value);
    });
  
    return groupedCustomers;
  }; 
  
