  getCustomerRecords(key: string, value: any) {
    this.tokenService.getCustomerByCustomerID(key, value).subscribe((data) => {

      if (data != null) {
        if (data?.length > 1) {
          setTimeout(() => {
            this.chatData[this.countValue].bot =
              'There are multiple names same as your name. Try with your Customer Id/KYC Id/Account Id';
            (this.chatData[
              this.countValue
            ].botTextTime = new Date().toLocaleTimeString()),
              this.countValue++;
          }, 2000);
        } else if (data?.length == 1) {
          this.getCustomerDetails(data[0].customerId);
        }
      } else {
        setTimeout(() => {
          this.chatData[this.countValue].bot = 'User not found!';
          (this.chatData[
            this.countValue
          ].botTextTime = new Date().toLocaleTimeString()),
            this.countValue++;
        }, 2000);
      }
    });
  }
  getCustomerDetails(Id: number) {
    this.tokenService
      .getCustomerDetailsByCustomerId(Id)
      .subscribe((response) => {

        let exactName = response.customerInfoList;
        let firstName: string = exactName[0].firstName;
        let middleName: string = exactName[0].middleName;
        let lastName: string = exactName[0].lastName;
        setTimeout(() => {
          this.chatData[this.countValue].bot =
            'Welcome ' +
            firstName.toLowerCase() +
            ' ' +
            (middleName == undefined || middleName == null ? '' : middleName) +
            ' ' +
            lastName;
          (this.chatData[
            this.countValue
          ].botTextTime = new Date().toLocaleTimeString()),
            (this.customerIdonBot = exactName[0].customerId);
          this.chatData[this.countValue].cusId = exactName[0].customerId;
          this.countValue++;
        }, 2000);
      });
  }

  currencyChanged(ccy) {
    this.selectedCurrency = ccy;
    for (let i = 0; i < this.currencyInfo.length; i++) {
      if (ccy == this.currencyInfo[i].currency) {
        this.denominationData = this.currencyInfo[i].denominations;
        this.denominationCurrency['amount'] = (<any>Object).keys(this.denominationData).reverse();
        this.denominationCurrency['value'] = (<any>Object).values(this.denominationData).reverse();
        this.currencyTransactionInfo = this.currencyInfo[i].currencyTransactionInfo;
        this.selectedCurrencyType = this.currencyInfo[i].currency;
        this.totalAmountOfCurrency = this.currencyInfo[i].totalValue;
      }
    }
    let payload: any = {};
    let countryName = '';
    this.selectedCountryImage = '';
    if (ccy == 'INR') {
      countryName = 'india';
      this.currencyNameByCurrencyCode = 'Indian Rupee';
    } else if (ccy == 'USD') {
      countryName = 'United States';
      this.currencyNameByCurrencyCode = 'United States Dollar';
    } else if (ccy == 'AED') {
      countryName = 'United Arab Emirates';
      this.currencyNameByCurrencyCode = 'UAE Dirham';
    } else if (ccy == 'EUR') {
      countryName = 'European Union';
      this.currencyNameByCurrencyCode = 'Euro';
      this.selectedCountryImage = 'assets/images/Flag_of_Europe.png';
    } else {
      countryName = '';
      this.currencyNameByCurrencyCode = '';
    }
    if (ccy != '' && ccy != 'EUR') {
      payload.country = countryName;
      this.tokenService
        .getCountryFlagByCountryName(payload)
        .subscribe((response) => {
          this.selectedCountryImage = response.data.flag;
        });
    }
  }

