import { ChangeDetectorRef, Component, Inject, Input, OnInit } from '@angular/core';

import { ViewChild } from '@angular/core';
import { FormGroup, NgForm } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { MatAccordion } from '@angular/material/expansion';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { OperationsComponent } from 'app/views/others/operations/operations.component';
import { CashDeposit } from 'app/shared/models/cashDeposit';
import { forkJoin } from 'rxjs';
import { FingerprintAuthenticationComponent } from '../../Dialogs/fingerprint-authentication/fingerprint-authentication.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FingerPrintCaptureService } from 'app/shared/services/finger-print-capture.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { FingerPrintVerificationComponent } from '../finger-print-verification/finger-print-verification.component';
import { ApiService } from 'app/shared/services/api.service';
import { DashboardComponent } from '../../dashboard/dashboard.component';
import { TransferComponent } from '../../Dialogs/transfer/transfer.component';
import { currency_symbols } from '../currency-symbols';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { CustomerAuthenticatedDialogComponent } from '../../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
  selector: 'app-cash-deposit',
  templateUrl: './cash-deposit.component.html',
  styleUrls: ['./cash-deposit.component.scss']
})
export class CashDepositComponent implements OnInit {

  @Input("cashDepositData") cashDepositData: any;
  @ViewChild(MatAccordion) accordion: MatAccordion;


  title: string;
  operation: string;
  detailsPanelOpened: boolean = false;
  //chargeDetailsPanelOpened: boolean = false;
  //denominationPanelOpened: boolean = false;
  cashDeposit: CashDeposit;
  chargeDetailArray: any[] = [];
  dataSource: any;
  accountTypeId: number;
  pauseTrans: boolean=false;
  currencyType : string = "";
  currencyInfo : any;
  denominationLimit : number;
  showLoader : boolean = false;
  denominationKey:number;
  denominationValue:number;
  hold:boolean=false;
  denomCodeArray: any[] = [
    { key: 'count2000', value: 2000 },
    { key: 'count500', value: 500 },
    { key: 'count200', value: 200 },
    { key: 'count100', value: 100 },
    { key: 'count50', value: 50 },
    { key: 'count20', value: 20 },
    { key: 'count10', value: 10 },
    { key: 'count5', value: 5 },
    { key: 'count2', value: 2 },
    { key: 'count1', value: 1 }

  ];

  @ViewChild('cdForm') cdForm: NgForm;

  totalAmt: Number;

  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];
  customerId: number;
  currencyImage: string=" ";
  accountCurrencyImage:string=" ";

  countryValues: number;
  amount: number;
  profileURL: any;
  tokenLists: any;
  tokenLists1: any;
  denomRequired: any;
  denomination: boolean;
  length: number;
  totalChargeAmount: number=0;

  tellerId:string;
  tokenArrayHavingRole: any[]=[];
  transferFlag: boolean=false;
  cancel: boolean=false;
  accountCountryValues: any;
  transactionDetails: any;
  disableSave: boolean = false;
  constructor(
    private tokenService: TokenService,
    private operationsComponent: OperationsComponent,
    public dialog: MatDialog,
    private fpCaptureService: FingerPrintCaptureService,
    private cdr: ChangeDetectorRef,
    private snack: MatSnackBar,
    private jwtService: JwtAuthService,
    private spinner: NgxSpinnerService,
    private ls: LocalStoreService,private api:ApiService, public dashboard:DashboardComponent,
  ) {

  }

  ngOnChanges() {
    this.cashDeposit = this.cashDepositData;
    this.cashDeposit.transactionCurrency=this.cashDepositData.transactionCurrency;
    //this.cashDeposit.totalAmount = 0;
    this.currencyImage = 'currency-flag-'+this.cashDepositData.transactionCurrency.toLowerCase();
    this.accountCurrencyImage='currency-flag-'+this.cashDepositData.accountCurrency.toLowerCase();
  }
  getCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValues : any)=> {
      this.countryValues = allCountryValues;
      });
  }
   getAccountCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValuesForAccount : any)=> {
      this.accountCountryValues = allCountryValuesForAccount;
      });
  }

  ngOnInit() {
    this.spinner.show();
    this.dashboard.slipEnable = false;
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.denomRequired = localStorage.getItem('denomRequired');   
    this.disableSave = this.denomRequired =='yes' ? true : false;     
    this.getCountrynameValues();
    this.getAccountCountrynameValues();
    this.customerId = this.ls.getItem('customer_id');
    this.cashDeposit = this.cashDepositData;
    this.cashDeposit.accountAmount=this.cashDeposit.transactionAmount;
    this.profileURL=this.cashDeposit.profileImage;
    this.currencyInfo = this.cashDepositData.currencyInfo;
    this.cashDeposit.transactionCurrency=this.cashDepositData.transactionCurrency;
    if(this.cashDepositData.transactionCurrency){
    this.currencyImage = 'currency-flag-'+this.cashDepositData.transactionCurrency.toLowerCase();
    }
    if(this.cashDepositData.accountCurrency){
    this.accountCurrencyImage='currency-flag-'+this.cashDepositData.accountCurrency.toLowerCase();
    }
    for(let i = 0; i < this.currencyInfo.length; i++){
       if(this.currencyInfo[i].currency == this.cashDeposit.transactionCurrency){
          this.denominationLimit = this.currencyInfo[i].denominations;
       }
    }
    //this.cashDeposit.totalAmount = 0;
    this.getChargeDetails();
    this.getCashDepositDenomination();
    // this.getBankInformation('KN')
    this.checkPauseTransaction();
    this.getTokensWithPermissions();
    this.lockTokenNumber();
    this.getExchangeRate();
  }
  checkPauseTransaction() {
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.tokenService.checkPauseTransaction(this.tellerId).subscribe(resp=>{
      const isPause=resp.data.some(d=>d.tokenNumber === this.cashDeposit.tokenNo)
      if(isPause === true)
           {
          this.pauseTrans=true;
          }      
        })
  }
  demArr=[2000,500,200,100,50,20,10,5,2,1];
  getCashDepositDenomination(){    let total = 0;
      for (const key in this.cashDeposit.denomination) {
    
        this.denomCodeArray.forEach(element => {
          if(this.cashDeposit.denomination[key].denominationKey == element.value){
          total = total + element.value * this.cashDeposit.denomination[key].denominationValue;
          // if(this.cashDeposit.denomination[key].denominationKey == 200){
          //     this.cashDeposit.count200 = this.cashDeposit.denomination[key].denominationValue;
          //   }
          var countVal='';
            for (const denom in this.demArr) {
              if (Object.prototype.hasOwnProperty.call(this.demArr, denom)) {
                const denomVal = this.demArr[denom];
                if(this.cashDeposit.denomination[key].denominationKey == denomVal){
                  countVal ='count'+denomVal;
                  this.cashDeposit[countVal] = this.cashDeposit.denomination[key].denominationValue;
                }
              }
            }
            
           }
        });
        this.cashDeposit.totalAmount = total;
      }
  }
  pauseTransaction(){
    this.pauseTrans = !this.pauseTrans;
    this.tokenService.pauseTransaction(this.cashDeposit.tokenNo,this.pauseTrans).subscribe(resp=>{
      if(resp.message!="Status updated successfully"){
        this.pauseTrans = !this.pauseTrans;
      }
      })
  }
  addCurrencyImage(currency) {
    return currency.toLowerCase();
  } 
  getChargeDetails() {
    if (this.cashDeposit.accountType == "Savings") {
      this.accountTypeId = 1;
    }
    else if (this.cashDeposit.accountType == "Current") {
      this.accountTypeId = 2;
    }
    else if (this.cashDeposit.accountType == "Joint") {
      this.accountTypeId = 3;
    }
    else {
      this.accountTypeId = null;
    }
    this.tokenService.getChargeDetails(this.cashDeposit.accountNumber).subscribe((resp) => {
      console.log(resp);

      if(resp){
        this.chargeDetailArray = resp;
      for (let index = 0; index < this.chargeDetailArray.length; index++) {
        if(this.chargeDetailArray[0].waiver==false || this.chargeDetailArray[0].waiver==null){
         
          this.totalChargeAmount += this.chargeDetailArray[index].amount
        
          }
      }
    }
      this.cashDeposit.totalChargeAmount=this.totalChargeAmount
      this.cashDeposit.accountAmount=this.cashDeposit.accountAmount+this.totalChargeAmount
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
      this.length=this.dataSource.filteredData.length 
    });
  }

  waiverChange(checked: boolean, i: number) {
    this.chargeDetailArray[i].waiver = checked;
    if(!checked){
      this.cashDeposit.accountAmount=this.cashDeposit.accountAmount+this.chargeDetailArray[i].amount;
      this.totalChargeAmount += this.chargeDetailArray[i].amount;
      this.cashDeposit.totalChargeAmount=this.totalChargeAmount;
    }
      else{
        this.cashDeposit.accountAmount-=this.chargeDetailArray[i].amount
        this.totalChargeAmount -= this.chargeDetailArray[i].amount;
        this.cashDeposit.totalChargeAmount=this.totalChargeAmount
      }
  }

  // openDialog(): void {
  //   this.customerId=90876;
  //   this.dialogService.fingerprintDialog(this.customerId);
  // }
    



  onCountryChange(country) {
    this.cashDeposit.exchangeRate=0;
    this.cashDeposit.accountAmount=this.cashDeposit.transactionAmount+this.cashDeposit.totalChargeAmount;
    let currency = country.currencyCode;
    this.currencyImage = 'currency-flag-'+currency.toLowerCase();
    this.getExchangeRate();
  }
  onAccountCountryChange(country) {

    let currency = country.currencyCode;
    this.accountCurrencyImage = 'currency-flag-'+currency.toLowerCase();
  }
  onclickofConfirm(cdForm: NgForm) {
    let denominationData: any = {};
    let denominationDataArray: any[] = [];

    for (let i = 0; i < this.denomCodeArray.length; i++) {
      if (cdForm.value[this.denomCodeArray[i].key]) {
        denominationData.transactionId = this.cashDepositData.id;
        denominationData.denominationKey = this.denomCodeArray[i].value;
        denominationData.denominationValue = +cdForm.value[this.denomCodeArray[i].key];
        denominationData.currency = this.cashDepositData.transactionCurrency;
        // denominationData.amountAvailiable = 15200 - (cdForm.value[this.denomCodeArray[i].key] * this.denomCodeArray[i].value);
        denominationDataArray.push(denominationData);
        denominationData = {};
      }
    }

    delete cdForm.value.count2000;
    delete cdForm.value.count500;
    delete cdForm.value.count200;
    delete cdForm.value.count100;
    delete cdForm.value.count50;
    delete cdForm.value.count20;
    delete cdForm.value.count10;
    delete cdForm.value.count5;
    delete cdForm.value.count2;

    this.cashDeposit = cdForm.value;
    this.cashDeposit.denomination = denominationDataArray;
    this.cashDeposit.transactionId = this.cashDepositData.transactionId;
    this.cashDeposit.tellerId = this.cashDepositData.tellerId;
    this.cashDeposit.holderName=this.cashDepositData.holderName;
    this.cashDeposit.tokenNo=this.cashDepositData.tokenNo;
    this.cashDeposit.customerId=this.cashDepositData.customerId;
    if(this.pauseTrans==true){
      this.showLoader = true;
      this.tokenService
        .saveCashDepositData(this.cashDeposit)
        .subscribe((resp1) => {
          if (resp1) {
            // this.successDialog();
            this.showLoader = false;
            this.operationsComponent.closeDrawer();
            this.dashboard.getCounterDashBoard();
            this.dashboard.getDashboardRecords();
            this.showTransactionSlip(resp1);
          }
        },(err)=>{
          this.snack.open(`Internal Server Error`, 'OK', {
            duration: 2000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          this.showLoader=false;
        });
        return;
    }

    if(this.transferFlag === false || this.cancel === true){
      this.tokenLists=this.ls.getItem('TOKENLIST')
      this.showLoader = false;
     
      if(this.tokenLists!=null){
      if(this.tokenLists.length >1 || this.tokenLists.length === 1){
        for(let i=0 ; i< this.tokenLists.length ; i++ ){
          if(this.cashDepositData.tokenNo == this.tokenLists[i].tokenNumber)
          {
            this.tokenLists.splice(i, 1);
            break;
          }
        }
        localStorage.removeItem('TOKENLIST');
        localStorage.setItem('TOKENLIST',JSON.stringify(this.tokenLists));
        this.tokenLists=this.ls.getItem('TOKENLIST');
       if(this.tokenLists.length >= 1){
        this.cashDeposit =null;
        this.cashDepositData="";
        this.operationsComponent.closeDrawer();
       this.dashboard.pickingChildTokenNumber(this.tokenLists[0].tokenNumber);
       }
    
        }
    
      }
      if(this.tokenLists == null || this.tokenLists.length === undefined || this.tokenLists.length=== 0){
        this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
          (data: any) => {
            this.tokenArrayHavingRole = data;
            if(this.tokenArrayHavingRole.length > 0){
              this.dashboard.pickingChildTokenNumber(this.tokenArrayHavingRole[0].tokenNumber);
            }
            else if(this.tokenArrayHavingRole.length === 0){
              this.operationsComponent.closeDrawer();
              this.dashboard.getCounterDashBoard();
              this.dashboard.getDashboardRecords();
            }
          },
          (error: any) => {
          }
        );
  
            }

     }
    else{
      this.showLoader = true;
      this.tokenService.saveCashDepositData(this.cashDeposit).subscribe((resp1) => {
        if(resp1){
          this.showTransactionSlip(resp1)
       
          this.showLoader = false;
          
       
              }
        
       
      },(err)=>{
        this.snack.open(`Internal Server Error`, 'OK', {
          duration: 2000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.showLoader=false;
      });

    // forkJoin(
    //   this.tokenService.saveCashDepositData(this.cashDeposit),
    //   this.tokenService.saveChargeDetails(this.chargeDetailArray)
    // ).subscribe((res) => {
    //   if (res[0] && res[1]) {
    //     this.cdForm.reset();
    //     this.accordion.closeAll();
    //     this.operationsComponent.closeDrawerAfterSuccess();
    //   }
    // },
    //   (error) => {
    //   }
    // );
    }
  
}


  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }

  cancelOperation() {
    this.cancel=true;
    this.tokenService.cancelOrLock(this.cashDeposit.tokenNo,this.cancel,this.tellerId).subscribe(resp=>{
      console.log(resp);
 },err=>{

 })
    this.cdForm.reset();
    this.accordion.closeAll();
    this.dashboard.getCounterDashBoard();
    this.dashboard.getDashboardRecords();
    this.operationsComponent.closeDrawer();
  }


  totalAmountValue(cdForm: NgForm) {
    this.denomination=true;
    this.cashDeposit.totalAmount =
      ((cdForm.value.count2000 ? cdForm.value.count2000 * 2000 : 0) +
        (cdForm.value.count500 ? cdForm.value.count500 * 500 : 0) +
        (cdForm.value.count200 ? cdForm.value.count200 * 200 : 0) +
        (cdForm.value.count100 ? cdForm.value.count100 * 100 : 0) +
        (cdForm.value.count50 ? cdForm.value.count50 * 50 : 0) +
        (cdForm.value.count20 ? cdForm.value.count20 * 20 : 0) +
        (cdForm.value.count10 ? cdForm.value.count10 * 10 : 0) +
        (cdForm.value.count5 ? cdForm.value.count5 * 5 : 0) +
        (cdForm.value.count2 ? cdForm.value.count2 * 2 : 0) +
        (cdForm.value.count1 ? cdForm.value.count1 * 1 : 0)
        );
     if(this.cashDeposit.totalAmount != this.cashDeposit.transactionAmount){
      this.disableSave = true;
     }   
     else{
      this.disableSave = false
     }
  }


  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  openDialog(): void {
   var name='cat';
    let dialogRef = this.dialog.open(FingerPrintVerificationComponent, {
      width: '500px',
      height:'500px',
      data: { name: name }
    });

    dialogRef.afterClosed().subscribe(result => {
      name = result;
    });
  }


  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  dataByCountryValues(country)
  {
    return country.currencyCode;
  }
  dataByAccountCountryValues(countryVal)
  {
    return countryVal.currencyCode;
  }
  // getBankInformation(branchCode:any){
  //   this.api.fetchBankInfoByBranchCode(branchCode).subscribe(resp => {
  //     console.log(resp)
  //     this.denomRequired = resp.denomRequired
      
  //   }) 
  // }
    transfer(){
    const dialogRef = this.dialog.open(TransferComponent, {
      width: '60%',
      height: '83%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
      data: {
        tokenNumber:this.cashDeposit.tokenNo
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
   
    });
  }
  getTokensWithPermissions(){
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
      (data: any) => {
        this.tokenArrayHavingRole = data;
        if(this.tokenArrayHavingRole.length >= 1){
       this.transferFlag = data.some(d=>d.tokenNumber === this.cashDeposit.tokenNo);
       this.spinner.hide();
      }
      },
      (error: any) => {
        this.spinner.hide();
      }
    );
   }
   lockTokenNumber(){
   
    this.tokenService.cancelOrLock(this.cashDeposit.tokenNo,this.cancel,this.tellerId).subscribe(resp=>{
         console.log(resp);
    },err=>{

    })
   }
   successDialog(): void {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: '60%',
      height: '75%',
      backdropClass: 'dialog-bg-trans',
      // disableClose: true,
    });
  
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
     
      // this._document.defaultView.location.reload();
    });
  }
  showTransactionSlip(resp1) {
    this.dashboard.slipEnable = true;

    this.dashboard.transactionDetails = resp1
    this.dashboard.transactionDetails.pauseTrans=this.pauseTrans
    this.dashboard.getCounterDashBoard();
    this.dashboard.drawer.open()
   
  }
  cancelTransactionSlip(cashDeposit){
    this.dashboard.slipEnable = false;
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.dashboard.slipEnable = false;
    console.log("calling cancel from cash depo")
    
    if(!this.dashboard.slipEnable && !cashDeposit.pauseTrans){
      this.tokenLists=this.ls.getItem('TOKENLIST')
      if(this.tokenLists!=null){
        if(this.tokenLists.length >1 || this.tokenLists.length === 1){
          for(let i=0 ; i< this.tokenLists.length ; i++ ){
            if(cashDeposit.tokenNo == this.tokenLists[i].tokenNumber)
            {
              this.tokenLists.splice(i, 1);
              break;
            }
          }
          localStorage.removeItem('TOKENLIST');
          localStorage.setItem('TOKENLIST',JSON.stringify(this.tokenLists));
          this.tokenLists=this.ls.getItem('TOKENLIST');
         if(this.tokenLists.length >= 1){
          this.dashboard.pickingChildTokenNumber(this.tokenLists[0].tokenNumber);
         }
    
          }
      
        }
        
        if(this.tokenLists == null || this.tokenLists.length === undefined || this.tokenLists.length=== 0){
          this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
            (data: any) => {
              this.tokenArrayHavingRole = data;
              if(this.tokenArrayHavingRole.length > 0){
                this.dashboard.pickingChildTokenNumber(this.tokenArrayHavingRole[0].tokenNumber);
              }
              else if(this.tokenArrayHavingRole.length === 0){
                this.dashboard.drawer.close()
                return;
              }
            },
            (error: any) => {
            }
          );
    
              }
            }
    else{
      
      this.dashboard.drawer.close()
      return;
    }
  }
  getExchangeRate(){
    if(this.cashDeposit.transactionCurrency != this.cashDeposit.accountCurrency){
      this.api.getExchangeRateforCurrency(this.cashDeposit.transactionCurrency,this.cashDeposit.accountCurrency).subscribe(res => {
        if(res[res.length-1].buyRate){
        this.cashDeposit.exchangeRate=res[res.length-1].buyRate;
        this.cashDeposit.accountAmount=(this.cashDeposit.transactionAmount * res[res.length-1].buyRate)+this.cashDeposit.totalChargeAmount;
        }
      })
    }
  }
  holdToken(cwForm: NgForm){
    this.cancel=true;
    this.tokenService.cancelOrLock(this.cashDeposit.tokenNo,this.cancel,this.tellerId).subscribe(resp=>{
      console.log(resp);
 },err=>{
  if(err.error.text=='Updated Successfully'){
    this.hold=true;
  this.snack.open('Token holded Successfully', 'OK', {
    duration: 2000,
    verticalPosition: 'top',
    horizontalPosition: 'right'
  });
  setTimeout(()=>{
    this.onclickofConfirm(cwForm);
  },2000)
}

 })
  }
}
