import { ChangeDetectorRef,Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { FingerPrintCaptureService } from 'app/shared/services/finger-print-capture.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
@Component({
  selector: 'app-finger-print-verification',
  templateUrl: './finger-print-verification.component.html',
  styleUrls: ['./finger-print-verification.component.scss']
})
export class FingerPrintVerificationComponent implements OnInit {

   titleMessage: string = "Please use your fingerprint to authenticate";
  contentMessage: string = "Please place your thumb on the fingerprint reader to proceed.";
  customerId: any;
  constructor(  private dialogRef: MatDialogRef<FingerPrintVerificationComponent>,
    private fpCaptureService: FingerPrintCaptureService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private cdr: ChangeDetectorRef,
    private snack: MatSnackBar,
    private ls: LocalStoreService,
    private router: Router) { }

  ngOnInit(): void {
    this.customerId=this.ls.getItem('customer_id');
  }

  goToClose() {
    this.dialogRef.close();
  }

  goToScanner() {
    this.fpCaptureService.CallingSGIFPCapture()
      .subscribe(capturedData => {
        console.log('capturedData:: ', capturedData);

        const customerId:any=this.customerId;
  
        if (capturedData.ErrorCode == 0) {
        this.getCustInfoById(customerId,capturedData.templateBase64);
        }
      })
  }
  getCustInfoById(custId,templateBase64) {
   this.fpCaptureService.getCustInfoByCustomerId(custId)
   .subscribe(resp=>{
    console.log('customer resp :: ',resp);//fpTemplateBase64

    for (const key in resp.data) {
      if (Object.prototype.hasOwnProperty.call(resp.data, key)) {
        const element = resp.data[key];
        for (const key1 in element) {
          if (Object.prototype.hasOwnProperty.call(element, key1)) {
            const ele = element[key1];
            this.matchFpFromUi(templateBase64,ele.fpTemplateBase64)
          }
        }
      }
    }
   })
  }

  matchFpFromUi(template1, data) {
    this.fpCaptureService.CallingSGIFPMatch(template1, data)
      .subscribe(fpResp => {
        console.log('fpResp : ', fpResp);
        if (fpResp.MatchingScore >= 100) {
          let custId = this.customerId;

          // TO-DO: need to call open dialog on success
          
          this.dialogRef.close({
            message: 'Confirm',
            custId
          });
          this.snack.open(`User found : ${custId}`, 'OK', {
            duration: 1000,
            verticalPosition: 'top',
            horizontalPosition: 'right'
          });
          return;
        }
      })
  }

  skip(){
    let data='skip';
    this.router.navigateByUrl('/others/services');
    this.dialogRef.close({
      message: 'Confirm',
      data
    });
  }

 

  /* @method : to get error code of finger print device */
  getError(errorCode) {
    let error = this.fpCaptureService.errorCodeService(errorCode);
    console.log('error code', error)
  }


}
