import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { TokenObject } from 'app/shared/models/token-object';
import { ApiService } from 'app/shared/services/api.service';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { CustomerOperationsModelComponent } from '../customer-operations-model/customer-operations-model.component';


@Component({
  selector: 'app-token-model',
  templateUrl: './token-model.component.html',
  styleUrls: ['./token-model.component.scss']
})
export class TokenModelComponent implements OnInit {
  screenName:string;
  tokenObjects: TokenObject[] = [];
  tokens = 'Tokens';
  pickToken = 'Pick-up Token';
  searchText="";
  tokenArray:  TokenObject[] = [];tellerId: string;
  denomRequired: any;
  givenRole:any[] = [];
  cashdeposit = 'Cash Deposit';
  chequedeposite = 'Cheque Deposit';
  forextransaction = 'Forex Transaction';
  utilitypayment = 'Utility Bill Payment';
  cashwithdraw = 'Cash Withdrawal';
  chequewithdrawal = 'Cheque Withdrawal';
  loanpayment = 'Loan Payment';
  deposittopup = 'Deposit Top Up';
  customerOperations: boolean;
  serviceCashDeposits: boolean;
  token: boolean;
  recentTxn: boolean;
  revertTxn: boolean;
  inHandCash: boolean;
  servicecashWithdraw: boolean;
  serviceCheqwithdraw: boolean;
  tellerCurrentTillPosition: boolean;
  tellerBankersCheque: boolean;
  tellerTotalTxn: boolean;
  tellerDemandDraft: boolean;
  serviceChequeDeposit: boolean;
  
  constructor(private router:Router,private apiService: ApiService,private jwtService: JwtAuthService,private tokenService: TokenService,public dialog: MatDialog, private dialogRef: MatDialogRef<TokenModelComponent>, @Inject(MAT_DIALOG_DATA) private data: any, private api: ApiService) {
    if(this.data==='service'){
         this.screenName='Service';
    }
    else{
      this.screenName='token';
      this.getCounterDashBoard();
    }
   }

  ngOnInit(): void {
    this.permissions();
  }
  
  check(event){
    if(event.target.value.trim()=== '')
    {
      this.tokenObjects=this.tokenArray
       return;
    }
    this.tokenObjects=this.tokenArray
    this.tokenObjects=this.tokenObjects.filter(obj=>obj.tokenNumber.includes(this.searchText.trim()));
  
  }
  close(){
    this.dialogRef.close();
  }
  pickingTokenNumber(tokenNumber) {
    this.dialogRef.close();
    this.api.setToken({
      token: tokenNumber,
    });
  
  }
  goToOperation(){
    this.dialogRef.close();
    this.api.setPickToken({
      pickToken: true,
    });
  }
  selectingMenu(event){
this.screenName="Service"
  }
  selectingMenu1(event){
    this.screenName="token"
    this.getCounterDashBoard();
  }
  onCashWithdrwal() {
    
 this.dialogRef.close('new Cash Withdrwal');
  }
  onCashDeposit() {
    this.dialogRef.close('new Cash Deposit');
  }
  onChequeDeposit() {
    this.dialogRef.close('new Cheque Deposit');
  }
  onChequeWithDrawal() {
    this.dialogRef.close('new Cheque Withdrawal');
  }
  onForexTransaction(){
    this.dialogRef.close('new Forex Transaction');
  }
  getCounterDashBoard() {
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }

    this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
      (data: any) => {
        this.tokenObjects = data;
        this.tokenArray=this.tokenObjects;
      },
      (error: any) => {
      }
    );
  }
  permissions(){
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }

    this.apiService
    .fetchTellerProfileInfo(this.tellerId)
    .subscribe((result) => {
      this.denomRequired = result.denomRequired
      localStorage.setItem('denomRequired',result.denomRequired)
      for(let i=0;i<result.permissionInfo.length; i++){
        for(let j=0;j<result.screenInfo.length; j++){
          if(result.permissionInfo[i].permissionId.screenId === result.screenInfo[j].screenId){
            var obj={
              'tabId':result.screenInfo[j].tabId,
              'screenName':result.screenInfo[j].screenName,

            }
            this.givenRole.push(obj);
          }
        }
      }
      this.givenRole.forEach(element => {
        //All Role
        if(element.tabId===1){
          console.log(element)
          if (element.screenName === 'Customer Operations') {
            this.customerOperations = true;
          }
          if (element.screenName === 'Cash Deposit') {
            this.serviceCashDeposits = true;
          }
          if (element.screenName === 'Tokens') {
            this.token = true;
          }
          if (element.screenName === 'Recent Transactions') {
            this.recentTxn = true;
          }
          if (element.screenName === 'Revert Transactions') {
            this.revertTxn = true;
          }
          if (element.screenName === 'Cash In Hand') {
            this.inHandCash = true;
          }
          if (element.screenName === 'Cash Withdrawal') {
            this.servicecashWithdraw = true;
          }
          if (element.screenName === 'Cheque Deposit') {
            this.serviceChequeDeposit = true;
          }
          if (element.screenName === 'Cheque Withdrawal') {
            this.serviceCheqwithdraw = true;
          }
          if (element.screenName === 'Current Till Position') {
            this.tellerCurrentTillPosition = true;
          }
          if (element.screenName === 'Bankers Cheque') {
            this.tellerBankersCheque = true;
          }
          if (element.screenName === 'Demand Draft') {
            this.tellerDemandDraft = true;
          }
          if (element.screenName === 'Total Transactions') {
            this.tellerTotalTxn = true;
          }
        }
        
        
      });
    });
  }
}
