import { Component, Inject, OnInit, ViewEncapsulation } from "@angular/core";
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from "@angular/material/dialog";
import { ApiService } from "app/shared/services/api.service";
import { TransactionDetailComponent } from "../Dialogs/transaction-detail/transaction-detail.component";
import { RecentTrasactionTaskSummaryComponent } from "../recent-trasaction-task-summary/recent-trasaction-task-summary.component";
import { DownloadModelComponent } from "./download-model/download-model.component";

@Component({
  selector: "app-recent-transaction-model",
  templateUrl: "./recent-transaction-model.component.html",
  styleUrls: ["./recent-transaction-model.component.scss"],
})
export class RecentTransactionModelComponent implements OnInit {
  transactionDataArr: any[] = [];
  defaultImage = "./assets/images/face-7.jpg";
  filterArray = [
    "Recent Transaction",
    "Last 7 days",
    "Current Month",
    "Last Month",
    "Last 3 Month",
    "Select date range",
  ];
  selected2: any;
  fromDate: any;
  toDate: any;
  filterOption: any;
  tellerId: string;
  searchText: any;
  tokenArray: any;
  constructor(
    private api: ApiService,
    public dialog: MatDialog,
    private dialogRef: MatDialogRef<RecentTransactionModelComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) {
    this.transactionDataArr = this.data;
    this.tokenArray=this.data
  }

  ngOnInit(): void {
    this.tellerId = localStorage.getItem("tellerid");
  }
  close() {
    this.dialogRef.close();
  }
  showTransactionDetail(tokenId, img) {
    this.dialogRef.close();
    var screen = "dashboard";
    var enableApproveOrRejectBtn = false;
    const dialogRef = this.dialog.open(TransactionDetailComponent, {
      width: "75%",
      height: "90%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
      data: {
        dataKey: tokenId,
        screen: screen,
        enableBtn: enableApproveOrRejectBtn,
        data: "",
        profileImg: img,
      },
    });

    dialogRef.afterClosed().subscribe((result) => {});
  }

  onDownload() {
    const dialogRef = this.dialog.open(DownloadModelComponent, {
      width: "30%",
      disableClose: true,
      panelClass: "myapp-no-padding-dialog",
      position: { right: "20px", bottom: "10px" },
      data: this.transactionDataArr,
      height: "370px",
    });

    dialogRef.afterClosed().subscribe((result) => {});
  }
  openRevertTransactionDialog() {
    this.dialogRef.close();
    const dialogRef = this.dialog.open(RecentTrasactionTaskSummaryComponent, {
      width: "75%",
      height: "90%",
      backdropClass: "dialog-bg-trans",
      disableClose: true,
      data: {
        dataKey: "Summary",
      },
    });

    dialogRef.afterClosed().subscribe((result) => {});
  }
  filter(event) {
    console.log(this.selected2);
    switch (this.selected2) {
      // ,'Last 7 days','Current Month',
      // 'Current Month','Last 3 Month','Select date range'
      case "Recent Transaction": {
        this.filterOption = "";
        this.callFilterApi(this.filterOption);
        break;
      }
      case "Last 7 days":
        this.filterOption = "SEVENDAYS";
        this.callFilterApi(this.filterOption);
        break;
      case "Current Month":
        this.filterOption = "CURRMONTH";
        this.callFilterApi(this.filterOption);
        break;
      case "Last 3 Month":
        this.filterOption = "LASTTHREEMONTH";
        this.callFilterApi(this.filterOption);
        break;
      case "Select date range":
        this.filterOption = "dateRange";
        break;
      case "Last Month":
        this.filterOption = "LASTMONTH";
        this.callFilterApi(this.filterOption);
        break;
      default:
        break;
    }
  }
  callFilterApi(enumValue) {
    this.api
      .getRecentTransactionFilter(this.tellerId, enumValue)
      .subscribe((resp) => {
        this.transactionDataArr = resp;
      });
  }
  callFilterApiBasedOnDateRange() {
    this.api
      .getRecentTransactionFilterBasedOnDate(
        this.tellerId,
        this.fromDate,
        this.toDate
      )
      .subscribe((resp) => {
        this.transactionDataArr = resp;
      });
  }
  check(event){
    
    if(event.target.value.trim()=== '')
    {
      this.transactionDataArr=this.tokenArray
       return;
    }
    this.transactionDataArr=this.tokenArray
    const searchText =this.searchText.trim().toLowerCase();
    this.transactionDataArr=this.transactionDataArr.filter(obj=>obj.customerName.toLowerCase().includes(searchText));
  
  }
}
