import { Component, OnInit } from "@angular/core";
import { MAT_DATE_FORMATS } from "@angular/material/core";
import { MatDialogRef } from "@angular/material/dialog";
import { ApiService } from "app/shared/services/api.service";
import { JwtAuthService } from "app/shared/services/auth/jwt-auth.service";
import { DownloadService } from "app/shared/services/download.service";
import { ToastrService } from "ngx-toastr";
import * as moment from 'moment';
export const MY_DATE_FORMATS = {
  parse: {
    dateInput: 'DD/MM/YYYY',
  },
  display: {
    dateInput: 'DD/MM/YYYY',
    monthYearLabel: 'MMMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY'
  },
};

@Component({
  selector: "app-download-model",
  templateUrl: "./download-model.component.html",
  styleUrls: ["./download-model.component.scss"],
  providers: [
    { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }
  ]
})
export class DownloadModelComponent implements OnInit {
  filterOption: string;
  selected2: any;
  toDate: any;
  transactionDataArr: any[] = [];
  fromDate: any;
  private jwtService: JwtAuthService;
  filterArray = ["Recent Transaction", "All Currency", "Select date range"];
  tellerId: any;
  downloadWay: string = "";
  constructor(
    private toastr: ToastrService,
    private downloadService: DownloadService,
    private dialogRef: MatDialogRef<DownloadModelComponent>,
    private api: ApiService
  ) {}

  ngOnInit() {
    this.tellerId = localStorage.getItem("tellerid");
  }
  close() {
    this.dialogRef.close();
  }
  filter(event) {
    console.log(this.selected2);
    switch (this.selected2) {
      // ,'Last 7 days','Current Month',
      // 'Current Month','Last 3 Month','Select date range'
      case "Recent Transaction": {
        this.filterOption = "";
        this.callFilterApi(this.filterOption);
        break;
      }
      case "Last 7 days":
        this.filterOption = "SEVENDAYS";
        this.callFilterApi(this.filterOption);
        break;
      case "Current Month":
        this.filterOption = "CURRMONTH";
        this.callFilterApi(this.filterOption);
        break;
      case "Last 3 Month":
        this.filterOption = "LASTTHREEMONTH";
        this.callFilterApi(this.filterOption);
        break;
      case "Select date range":
        this.filterOption = "dateRange";
        break;
      case "All Currency":
        this.filterOption = "CURRENCY";
        break;
      case "Last Month":
        this.filterOption = "LASTMONTH";
        this.callFilterApi(this.filterOption);
        break;
      default:
        break;
    }
  }
  callFilterApi(enumValue) {
    this.api
      .getRecentTransactionFilter(this.tellerId, enumValue)
      .subscribe((resp) => {
        this.transactionDataArr = resp;
      });
  }
  callFilterApiBasedOnDateRange() {
    this.api
      .getRecentTransactionFilterBasedOnDate(
        this.tellerId,
        moment(this.fromDate).format('YYYY-MM-DD'),
        moment(this.toDate).format('YYYY-MM-DD'),
      )
      .subscribe((resp) => {
        this.transactionDataArr = resp;
        if (this.downloadWay === "excel") {
          this.downloadService.Excel(this.transactionDataArr);
        }
        if (this.downloadWay === "pdf") {
          this.downloadService.downloadFiles(
            "recentTransactionDownload",
            this.transactionDataArr
          );
        }
      });
  }
  download() {
    if (this.downloadWay === "") {
      this.toastr.error("", `Please select download format.`, {
        progressAnimation: "decreasing",
        progressBar: true,
        positionClass: "toast-top-right",
        timeOut: 3000,
      });
      return;
    }
    if(this.selected2=='Select date range'){
      var d1 = Date.parse(this.toDate);
      var d2 = Date.parse(this.fromDate);
      
      if (d1 < d2) {
        this.toastr.error("", `From Date should not be greater than To Date.`, {
          progressAnimation: "decreasing",
          progressBar: true,
          positionClass: "toast-top-right",
          timeOut: 3000,
        });
        return;
                 }
      else{
        this.callFilterApiBasedOnDateRange();
            }  
    }
    if(this.selected2 !='Select date range'){
      if (this.downloadWay === "excel") {
        this.downloadService.Excel(this.transactionDataArr);
      }
      if (this.downloadWay === "pdf") {
        this.downloadService.downloadFiles(
          "recentTransactionDownload",
          this.transactionDataArr
        );
      }
    }
    
 
  }
}
