import { Component, EventEmitter, Inject, OnInit, Output, ChangeDetectorRef } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TokenInformation } from 'app/shared/models/tokenInformation';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import * as _ from 'lodash';
import { TransactionDetailComponent } from '../Dialogs/transaction-detail/transaction-detail.component';
import { DialogService } from '../Dialogs/dialog.service';
import { HttpParams } from '@angular/common/http';

@Component({
  selector: 'app-recent-trasaction-task-summary',
  templateUrl: './recent-trasaction-task-summary.component.html',
  styleUrls: ['./recent-trasaction-task-summary.component.scss']
})
export class RecentTrasactionTaskSummaryComponent implements OnInit {
  searchText;
  statusArr: any = ['REVERT', 'INITIATED', 'APPROVED', 'REJECTED'];

  REVERTArr: string[] = [];
  INITIATEDArr: string[] = [];
  REJECTEDArr: string[] = [];
  APPROVEDArr: string[] = [];


  REVERTPage: number = 0;
  INITIATEDPage: number = 0;
  REJECTEDPage: number = 0;
  APPROVEDPage: number = 0;

  REVERTTotalPages: number = 0;
  INITIATEDTotalPages: number = 0;
  REJECTEDTotalPages: number = 0;
  APPROVEDTotalPages: number = 0;

  REVERTTotalRecords: number = 0;
  INITIATEDTotalRecords: number = 0;
  REJECTEDTotalRecords: number = 0;
  APPROVEDTotalRecords: number = 0;

  @Output() setValue: EventEmitter<string> = new EventEmitter();
  private _searchSubject: Subject<string> = new Subject();
  constructor(private tokenService: TokenService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<RecentTrasactionTaskSummaryComponent>,
    public dialog: MatDialog, private dialogService: DialogService,
    public cdr: ChangeDetectorRef) {
    this._setSearchSubscription();
  }

  ngOnInit(): void {
    this.getDataOnLoad();
  }


  public updateSearch(searchTextValue: string) {
    this._searchSubject.next(searchTextValue);
    // if(searchTextValue != null){
    //   this._searchSubject.next(searchTextValue);
    // }else{
    //   this.getDataOnLoad();
    // }
  }


  private _setSearchSubscription() {
    this._searchSubject.pipe(
      debounceTime(1500)
    ).subscribe((searchValue: string) => {
      this.searchKycid(searchValue);
     
    });
  }



  goToTransactionDetails(item) {
   
    var screen = 'summary';
    var enableApproveOrRejectBtn = true;
    // this.dialogService.transactionDetailsDialog(item.tokenNo,screen,enableApproveOrRejectBtn,item);
    const dialogRef = this.dialog.open(TransactionDetailComponent, {
      width: '75%',
      height: '90%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
      data: {
        dataKey: item.tokenNo? item.tokenNo: item.transactionId,
        screen: screen,
        enableBtn: enableApproveOrRejectBtn,
        data: item
      }
    });

    dialogRef.afterClosed().subscribe(result => {
    
      if(this.searchText != null){
        this._searchSubject.next(this.searchText);
      }else{
        this.getDataOnLoad();
      }
    });
  }

  /* @method :: to load all data on screen load */
  getDataOnLoad() {
    for (let index = 0; index < this.statusArr.length; index++) {
      const statusItem = this.statusArr[index];
      this.fetchRecentTransactionByStatus(statusItem, 0, 0);
    }
  }

  fetchRecentTransactionByStatus(status: string, page: number, size: number) {
    this.tokenService.getAllRevertTransactionData(status, page, size,true)
      .subscribe((trxnResp: any) => {
        // console.log("Response", trxnResp);
        this.updateStatusResponse(status, trxnResp);
      });
  }

  updateStatusResponse(status, data) {
    // console.log('status :: ',status , ' data :: ',data);
    if (status == 'REVERT') {
      this.REVERTArr = data.trxnList;
      this.REVERTTotalPages = data.totalPages;
      this.REVERTTotalRecords = data.totalRecords;
      // console.log("this.REVERTArr", this.REVERTArr , data.trxnList);
    } else if (status == 'INITIATED') {
      this.INITIATEDArr = data.trxnList;
      this.INITIATEDTotalPages = data.totalPages;
      this.INITIATEDTotalRecords = data.totalRecords;
      // console.log("this.INITIATEDArr", this.INITIATEDArr);
    } else if (status == 'APPROVED') {
      this.APPROVEDArr = data.trxnList;
      this.APPROVEDTotalPages = data.totalPages;
      this.APPROVEDTotalRecords = data.totalRecords;
      // console.log("this.APPROVEDArr", this.APPROVEDArr);
    } else if (status == 'REJECTED') {
      this.REJECTEDArr = data.trxnList;
      this.REJECTEDTotalPages = data.totalPages;
      this.REJECTEDTotalRecords = data.totalRecords;
      // console.log("this.REJECTEDArr", this.REJECTEDArr);
    }

    this.cdr.markForCheck();
  }

  /* @method :: to load data on scroll of data*/
  onScrollingFinished(e, status) {
    if (status == 'REVERT') {
      this.REVERTPage++;
      this.getDataOnScroll(status, this.REVERTPage, this.REVERTTotalPages);

    } else if (status == 'INITIATED') {
      this.INITIATEDPage++;
      this.getDataOnScroll(status, this.INITIATEDPage, this.INITIATEDTotalPages);

    } else if (status == 'APPROVED') {
      this.APPROVEDPage++;
      this.getDataOnScroll(status, this.APPROVEDPage, this.APPROVEDTotalPages);
    } else if (status == 'REJECTED') {
      this.REJECTEDPage++;
      this.getDataOnScroll(status, this.REJECTEDPage, this.REJECTEDTotalPages);
    }

  }

  /* @method :: to call service ,when scroll method called */
  getDataOnScroll(status, page, totalPage) {
    // console.log('---------scrolled----------')
    if (page === totalPage) {
      // console.log('No scroll requied')
      return;
    }

    this.tokenService.getAllRevertTransactionData(status, page, 0,true)
      .subscribe(data => {
        // console.log('count data :: ', data);
        this.cdr.markForCheck();
        this.updateDataOnScroll(status, data.trxnList);
      });

  }

  searchKycid(searchTerm) {

    var params1;
    if (searchTerm === '') {
      // console.log('empty')
      this.getDataOnLoad();
    }
    if (this.isNum(searchTerm)) {
      this.getTransactionDataForNumericType(searchTerm);
    } else {
      this.getTransactionDataForStringType(searchTerm);
    }

  }

  numberarr = ['transactionId', 'accountNumber', 'customerId']
  stringArr=['tokenNo','holderName'];
  isOnlyTransactionStatus:any=false;
  transactionStatus:any="INITIATED";
  getTransactionDataForStringType(searchTerm) {
    var params1;
    for (const key in this.stringArr) {
      if (Object.prototype.hasOwnProperty.call(this.stringArr, key)) {
        const param = this.stringArr[key];
        // console.log('param :: ', param);
        params1 = new HttpParams()
        .append(param, searchTerm)
        .append('transactionStatus',this.transactionStatus)
        .append('isOnlyTransactionStatus',this.isOnlyTransactionStatus);
        this.tokenService.getTransactionDataByTransactionId(params1)
          .subscribe(data => {
            // console.log('resp data :: ', data);
            if (data.trxnList.length > 0) {
              this.resetSearchData();
              for (let index = 0; index < data.trxnList.length; index++) {
                const element = data.trxnList[index];
                this.searchResponseUpdate(element.transactionStatus, element);
              }
            }
          });
      }
    }
  }

  getTransactionDataForNumericType(searchTerm) {
    var params1;
    for (const key in this.numberarr) {
      if (Object.prototype.hasOwnProperty.call(this.numberarr, key)) {
        const param = this.numberarr[key];
        // console.log('param :: ', param);
        params1 = new HttpParams().append(param, searchTerm)
        .append('transactionStatus',this.transactionStatus)
        .append('isOnlyTransactionStatus',this.isOnlyTransactionStatus);
        this.tokenService.getTransactionDataByTransactionId(params1)
          .subscribe(data => {
            // console.log('resp data :: ', data);
            if (data.trxnList.length > 0) {
              this.resetSearchData();
              for (let index = 0; index < data.trxnList.length; index++) {
                const element = data.trxnList[index];
                this.searchResponseUpdate(element.transactionStatus, element);
              }
            }
          });
      }
    }
  }

  resetSearchData() {
    this.INITIATEDArr = [];
    this.REVERTArr = [];
    this.APPROVEDArr = [];
    this.REJECTEDArr = [];
    this.cdr.markForCheck();
  }

  searchResponseUpdate(status: string, data: any) {
    if (status == 'INITIATED') {
      this.INITIATEDArr.push(data);
      // console.log('this.INITIATEDArr :: ', this.INITIATEDArr);
    } else if (status == 'REVERT') {
      this.REVERTArr.push(data);
      // console.log('this.REVERTArr :: ', this.REVERTArr);
    } else if (status == 'APPROVED') {
      this.APPROVEDArr.push(data);
      // console.log('this.APPROVArr :: ', this.APPROVEDArr);
    } else if (status == 'REJECTED') {
      this.REJECTEDArr.push(data);
      // console.log('this.REJECTEDArr :: ', this.REJECTEDArr);
    }
    this.cdr.markForCheck();
  }

  clear() {
    this.searchText = "";
    this.getDataOnLoad();
  }

  /* @method :: to update the existing array with scroll api response */
  updateDataOnScroll(status: string, data: any) {
    if (status == 'INITIATED') {
      const result = _.unionBy(this.INITIATEDArr, data, 'customerId');
      this.INITIATEDArr = result;
    } else if (status == 'REVERT') {
      const result = _.unionBy(this.REVERTArr, data, 'customerId');
      this.REVERTArr = result;
    } else if (status == 'APPROVED') {
      const result = _.unionBy(this.APPROVEDArr, data, 'customerId');
      this.APPROVEDArr = result;
    } else if (status == 'REJECTED') {
      const result = _.unionBy(this.REJECTEDArr, data, 'customerId');
      this.REJECTEDArr = result;
    }
    this.cdr.markForCheck();
  }

  ngOnDestroy() {
    this._searchSubject.unsubscribe();

    this.REVERTArr = [];
    this.INITIATEDArr = [];
    this.REJECTEDArr = [];
    this.APPROVEDArr = [];

    this.REVERTPage = 0;
    this.INITIATEDPage = 0;
    this.REJECTEDPage = 0;
    this.APPROVEDPage = 0;

    this.REVERTTotalPages = 0;
    this.INITIATEDTotalPages = 0;
    this.REJECTEDTotalPages = 0;
    this.APPROVEDTotalPages = 0;

  }

  isNum(val) {
    return !isNaN(val)
  }

  getRandomColor(idx) {
    var col0 = '#0d856b';
    var col1 = '#d66f1b';
    var col2 = '#9f52e7';
    var col3 = '#e9318d';
    var col4 = '#1175a3';
    var col5 = '#e93131';
    var col6 = '#2316d3';
    var col7 = '#f557f5';
    var col8 = '#d6c31b';
    var col9 = '#40d61b';

    if ((idx % 10) == 0) return col0;
    if ((idx % 10) == 1) return col1;
    if ((idx % 10) == 2) return col2;
    if ((idx % 10) == 3) return col3;
    if ((idx % 10) == 4) return col4;
    if ((idx % 10) == 5) return col5;
    if ((idx % 10) == 6) return col6;
    if ((idx % 10) == 7) return col7;
    if ((idx % 10) == 8) return col8;
    if ((idx % 10) == 9) return col9;
    return '#d86315';
    // var randomColor = Math.floor(Math.random()*16777215).toString(16);
    // return '#' + randomColor.slice(-6);
    // var randomColor = Math.floor(0x1000000 * Math.random()).toString(16);
    // return '#' + ('000000' + randomColor).slice(-6);
  }

  goToClose() {
    this.dialogRef.close({
      message: 'Confirm',
    });
  }
  dataByREVERTArr(index,item){
    return item;
  }
  dataByAPPROVEDArr(index,item){
    return item;
  }
  dataByREJECTEDArr(index,item){
    return item;
  }
}
