import { Component, Input, OnInit, ViewChild } from "@angular/core";
import { OperationsComponent } from "../../operations/operations.component";
import { MatDrawer } from "@angular/material/sidenav";
import { MatSnackBar } from "@angular/material/snack-bar";
import { TokenService } from "app/shared/services/token-service/token.service";
import { FormBuilder, FormGroup, NgForm, Validators } from "@angular/forms";
import { LocalStoreService } from "app/shared/services/local-store.service";
import { DialogService } from "../../Dialogs/dialog.service";
import { DataService } from "app/shared/services/data.service";
import { Router } from "@angular/router";
import { newCashWithdrawal } from "app/shared/models/newCashWithdrawal";
import { CashWithdrawal } from "app/shared/models/cashWithdrawal";
import { ApiService } from "app/shared/services/api.service";
import { MatTableDataSource } from "@angular/material/table";
import { DashboardComponent } from "../../dashboard/dashboard.component";
import { MatDialog } from "@angular/material/dialog";
import { CustomerAuthenticatedDialogComponent } from "../../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component";
import { FingerprintAuthenticationComponent } from "../../Dialogs/fingerprint-authentication/fingerprint-authentication.component";

@Component({
  selector: "app-new-cashwithdrawal",
  templateUrl: "./new-cashwithdrawal.component.html",
  styleUrls: ["./new-cashwithdrawal.component.scss"],
  
})
export class NewCashwithdrawalComponent implements OnInit {
  @ViewChild("drawer", { static: true }) public drawer!: MatDrawer;
  @ViewChild("cdForm") cdForm: NgForm;
  loading: boolean;

  user: string;
  accountBal: any;
  transAmt: any;
  holderName: string;
  arrayAccountBranch: { branchName: string }[];
  branchNameFromLogin: string;
  accountResp: newCashWithdrawal;
  cashWithdrawal1: CashWithdrawal;
  invalidAccNo: boolean = false;
  disableSave: boolean = true;
  invalidAccNoMsg: any;
  selectedIndex: number = 0;
  trxnAmtFlag: boolean = false;
  denomCodeArray: any[] = [
    { key: "count2000", value: 2000 },
    { key: "count500", value: 500 },
    { key: "count200", value: 200 },
    { key: "count100", value: 100 },
    { key: "count50", value: 50 },
    { key: "count20", value: 20 },
    { key: "count10", value: 10 },
    { key: "count5", value: 5 },
    { key: "count2", value: 2 },
    { key: "count1", value: 1 },
  ];
  denomination: any[] = [];
  a: any;
  cashWithdrawal: any = "Cash Withdrawal";
  cashWithdrawalNew: FormGroup;
  transamount: any;
  totalAmount: any;
  transAmtInteger: any;
  currencyImage: any = "";
  cashWithdrawalResponse: any;
  tokenNum: any;
  estimatedTime: any;
  enableContinue: boolean;
  tellerId: string;
  transactionId: number;
  cashwith: any;
  denomRequired: any;
  chargeDetailArray: any;
  dataSource: MatTableDataSource<unknown>;
  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];
  totalChargeAmount: number = 0;
  totalTransactionAmount: number = 0;
  exchangeRate: any;
  transValue: number;
  validPattern = "^(?![a-zA-Z\s]+$)[a-zA-Z0-9]+$"
  constructor(
    private operationsComponent: OperationsComponent,
    private dialogService: DialogService,
    private snack: MatSnackBar, 
    private api: TokenService,
    private apiService: ApiService,
    private router: Router,
    private ls: LocalStoreService,
    private dataService: DataService,
    private fb: FormBuilder,
    private dashboard: DashboardComponent,
    private dialog: MatDialog
  ) { }
  countryValues: any;
  branchName: any;
  Onlynumber(event): boolean {
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  ngOnInit(): void {
    this.branchNameFromLogin = localStorage.getItem("branchName");
    this.tellerId = localStorage.getItem("tellerid");
    if (this.branchNameFromLogin.includes("null")) {
      //this.branchName='Kasturi Nagar';
      //  this.arrayAccountBranch= [{'branchName':'Kasturi Nagar'}];
    } else {
      this.branchName = this.branchNameFromLogin;
    }
    this.denomRequired = localStorage.getItem('denomRequired');
    this.disableSave = this.denomRequired =='yes' ? true : false    
    this.cashWithdrawalNew = this.fb.group({
      accountNumber: ["", [Validators.required,Validators.minLength(4),Validators.maxLength(15),Validators.pattern(this.validPattern)]],
      accountType: ["", [Validators.required]],
      holderName: ["", [Validators.required]],
      accountCurrency: ["", [Validators.required]],
      transactionCurrency: ["", [Validators.required]],
      transactionAmount: ["", [Validators.required, Validators.min(0.1)]],
      productCode: ["CHW"],
      tokenOrigin: ["Teller"],
      accountBranch: ["", [Validators.required]],
      customerId: [""],
      tellerId: [""],
      transactionBranch: [""],
      transactionId: [""],
      exchangeRate: [""],
      totalTransactionAmount: [""],
      totalChargeAmount: [""],
      narrative:['']
    });

    this.cashWithdrawalNew.get("accountBranch").setValue(this.branchName);
    this.cashWithdrawalNew.get("transactionBranch").setValue(this.branchName);
    this.user = this.ls.getItem("ICUST_USER");
    this.getCountrynameValues();
    this.getAccountBranch();
    // this.fetchingHolderDetailsForCashWithdrwal();
    // this.cashWithdrawalNew.controls.holderName.setValue(this.user.tellerName);
    this.cashWithdrawalNew.valueChanges.subscribe((formValue) => {
      if (formValue.transactionAmount > this.accountBal) {
        this.trxnAmtFlag = true;
      } else {
        this.trxnAmtFlag = false;
      }
    });
    // this.getBankInformation('KN');
  }
  cancelOperation() {
    this.operationsComponent.closeDrawer();
    this.cashWithdrawalNew.reset();
    this.transAmtInteger = 0;
    this.accountBal = null;
    // this.router.navigate(['/dashboard']);
  }
  onCashWithdrwal() {
    var withoutComma = this.cashWithdrawalNew.get('transactionAmount').value.replace(/\,/g, '');
    let denominationData: any = {};
    let denominationDataArray: any[] = [];
    if (this.cdForm) {
      for (let i = 0; i < this.denomCodeArray.length; i++) {
        if (this.cdForm.value[this.denomCodeArray[i].key]) {
          denominationData.transactionId =
            this.cashWithdrawalNew.value.transactionId;
          denominationData.denominationKey = this.denomCodeArray[i].value;
          denominationData.denominationValue =
            +this.cdForm.value[this.denomCodeArray[i].key];
          denominationData.currency =
            this.cashWithdrawalNew.value.transactionCurrency;
          denominationDataArray.push(denominationData);
          denominationData = {};
        }
      }

      delete this.cdForm.value.count2000;
      delete this.cdForm.value.count500;
      delete this.cdForm.value.count200;
      delete this.cdForm.value.count100;
      delete this.cdForm.value.count50;
      delete this.cdForm.value.count20;
      delete this.cdForm.value.count10;
      delete this.cdForm.value.count5;
      delete this.cdForm.value.count2;
      console.log("denomData", denominationDataArray);
      var denomination_Ob = [
        {
          // transactionId: this.cashWithdrawal1.id,
          transactionId: this.cashWithdrawalNew.value.transactionId,
          denominationKey: denominationData.denominationKey,
          denominationValue: denominationData.denominationValue,
          currency: this.cashWithdrawalNew.value.transactionCurrency,
        },
      ];

      denomination_Ob = denominationDataArray;
      denominationDataArray = this.denomination;
    }

    let custId = this.accountResp.customerId;
    this.cashWithdrawalNew.get("customerId").setValue(+custId);
    this.cashWithdrawalNew.get("transactionAmount").setValue(this.transAmtInteger);
    this.cashWithdrawalNew.get("tellerId").setValue(this.tellerId);
    this.cashWithdrawalNew.get("productCode").setValue("CHW");
    this.cashWithdrawalNew.get("transactionBranch").setValue(this.cashWithdrawalNew.value.accountBranch)
    this.cashWithdrawalNew.value.denomination = denomination_Ob;
    this.cashWithdrawalNew.value.transactionId = this.transactionId;
    this.cashWithdrawalNew.value.transactionAmount = withoutComma;
    const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
      width: '60%',
      height: '70%',
      backdropClass: 'dialog-bg-trans',
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result != 'close'){ 
    this.api.saveofNewCashDeposit(this.cashWithdrawalNew.value).subscribe(
      (cashwith) => {
        if (cashwith) {
          this.cashWithdrawalNew.value.transactionId = this.transactionId;
          this.transactionId = cashwith.transactionId;
          this.cashWithdrawalResponse = cashwith;
          this.cashWithdrawalResponse.transactionType = 'Cash Withdrawal'
          this.tokenNum = this.cashWithdrawalResponse.tokenNo;
          this.estimatedTime = this.cashWithdrawalResponse.estimatedWaitTime;
          localStorage.setItem("tokenNumer", this.tokenNum);
          // localStorage.setItem("estimatedTiming", this.estimatedTime);
          this.enableContinue = false;
          if (this.cashWithdrawalResponse) {
            // this.dialogService.fingerprintDialog(null);
            // this.cancelOperation()
            // // this.dialogService.tokenGenerationDialog();
            // this.snack.open(`Transaction done successfully`, "OK", {
            //   duration: 2000,
            //   verticalPosition: "top",
            //   horizontalPosition: "right",
            // });
            // this.dataService.sendClickEvent();
            this.successDialog();
            this.operationsComponent.closeDrawer();
            this.dashboard.getCounterDashBoard();
            this.dashboard.getDashboardRecords();
          }
          this.cashWithdrawalNew.reset();
          this.transAmtInteger = 0;
          this.accountBal = null;
          this.holderName = "";
          this.currencyImage = null;
          // this.snack.open(`Record Saved`, 'OK', {
          //   duration: 2000,
          // verticalPosition: 'top',
          // horizontalPosition: 'right'
          // });
          // this.disableSave = true;
        }
      },
      (err) => {
        this.snack.open(`Internal Server Error`, "OK", {
          duration: 2000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
      }
    );
      }
    })
  
  }
  getCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValues: any) => {
      this.countryValues = allCountryValues;
    });
  }
  _keyUp() {
    if (this.cashWithdrawalNew.get('accountNumber').value == "") {
      this.cashWithdrawalNew.reset();
      this.accountBal = null;
      this.holderName = "";
      this.transAmtInteger = 0;
      this.totalTransactionAmount = 0;
      this.dataSource = null;
      this.cdForm.reset();
    }
  }
  fetchingHolderDetailsForCashWithdrwal(accountnum) {
    this.getChargeDetails()
    this.currencyImage = null
    this.loading = true;

    // let accountId=accountnum.target.value;
    this.api.fetchingDetailsForCash(accountnum).subscribe(
      (resp) => {
        console.log(resp);
        this.loading = false;
        if (resp == null) {
          this.snack.open("Invalid Account number: " + accountnum, "", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
        if (resp.accountStatus && resp.accountStatus != "APPROVED") {
          this.snack.open("Account Number is not Approved :" + accountnum, "", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
          this.cashWithdrawalNew.reset();
          //this.ngOnInit();
          this.getCountrynameValues();
          this.getAccountBranch();
          return;
        }
        if (resp) {
          this.accountResp = resp;
          this.accountBal = resp.accBalance;
          this.holderName = resp.accHolderName;
          this.invalidAccNo = false;
          this.invalidAccNoMsg = resp.message;
          if (this.invalidAccNoMsg == "No value present") {
            this.invalidAccNo = true;
          }
          this.cashWithdrawalNew
            .get("holderName")
            .patchValue(resp.accHolderName);
          this.cashWithdrawalNew
            .get("accountCurrency")
            .patchValue(resp.currency);
          this.cashWithdrawalNew
            .get("transactionCurrency")
            .patchValue(resp.currency);
          this.cashWithdrawalNew
            .get("accountType")
            .patchValue(resp.accountType);
          this.cashWithdrawalNew
            .get("transactionBranch")
            .setValue(this.branchName);
          this.cashWithdrawalNew
            .get("accountBranch")
            .patchValue(resp.accountBranch);
          //console.log(this.accountBranch);
          // if(resp.accountBranch != null && resp.accountBranch !='' && resp.accountBranch!=' ' && resp.accountBranch!=undefined){
          //   this.cashWithdrawalNew.get('accountBranch').patchValue(resp.accountBranch);
          // }else{
          //   this.branchName='Kasturi Nagar';
          //   this.cashWithdrawalNew.get('accountBranch').setValue(this.branchName);
          // }
        } else {
          this.invalidAccNo = true;
          console.log(this.invalidAccNo);
          this.snack.open("Incorrect Account Number", "OK", {
            duration: 2000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
      },
      (err) => {
        this.loading = false;
        this.snack.open(err.error.text, "", {
          duration: 4000,
          verticalPosition: "top",
          horizontalPosition: "right",
        });
        this.invalidAccNo = true;
        this.accountBal = null;
        this.holderName = "";
        this.cashWithdrawalNew.reset();
      }
    );
  }
  getChargeDetails() {
    this.api.getChargeDetails(this.cashWithdrawalNew.value.accountNumber).subscribe((resp) => {
      this.chargeDetailArray = resp;
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
      this.calculateTotalCharge();
      if (this.cashWithdrawalNew.get('transactionAmount').value) {
        this.calculateTotalTransactionAmount();
      }
    });
  }

  addCurrencyImage(currency) {
    return currency?.toLowerCase();
  }
   
  onCountryChange(country) {
    this.disableSave = this.denomRequired =='yes' ? true : false;    
    if (country.currencyCode != this.cashWithdrawalNew.get('accountCurrency').value) {
      this.cashWithdrawalNew.get('exchangeRate').setValue(null)
      this.apiService.getExchangeRateforCurrency(country.currencyCode, this.cashWithdrawalNew.get('accountCurrency').value).subscribe(res => {
        this.exchangeRate = res[res.length-1].sellRate
        console.log(this.exchangeRate);
        this.cashWithdrawalNew.get('exchangeRate').setValue(res[res.length-1].sellRate)
        console.log(this.cashWithdrawalNew);
        // this.calculateTotalCharge();
        this.calculateTotalTransactionAmount();
      })
    }
    else {
      this.cashWithdrawalNew.get('exchangeRate').setValue(null)
      this.calculateTotalCharge();
      this.calculateTotalTransactionAmount();
    }
    this.cdForm.reset();
    this.cashWithdrawalNew.get('totalAmount').setValue(0);
    let currency = country.currencyCode;
    this.currencyImage = "currency-flag-" + currency.toLowerCase();
  }

  getAccountBranch() {
    this.api.getAccountBranch().subscribe((branches) => {
      this.arrayAccountBranch = branches;
    });
  }

  omit_number(event) {
    var key;
    key = event.charCode;
    return key > 47 && key < 58;
  }

  totalAmountValue(cdForm) {
    this.a = this.cashWithdrawalNew.value.transactionAmount;
    this.a = this.a.replace(/\,/g, ""); // 1125, but a string, so convert it to number
    this.a = parseInt(this.a, 10);

    this.cashWithdrawalNew.value.totalAmount =
      (cdForm.value.count2000 ? cdForm.value.count2000 * 2000 : 0) +
      (cdForm.value.count500 ? cdForm.value.count500 * 500 : 0) +
      (cdForm.value.count200 ? cdForm.value.count200 * 200 : 0) +
      (cdForm.value.count100 ? cdForm.value.count100 * 100 : 0) +
      (cdForm.value.count50 ? cdForm.value.count50 * 50 : 0) +
      (cdForm.value.count20 ? cdForm.value.count20 * 20 : 0) +
      (cdForm.value.count10 ? cdForm.value.count10 * 10 : 0) +
      (cdForm.value.count5 ? cdForm.value.count5 * 5 : 0) +
      (cdForm.value.count2 ? cdForm.value.count2 * 2 : 0) +
      (cdForm.value.count1 ? cdForm.value.count1 * 1 : 0);
    this.transamount = this.transAmtInteger;
    this.totalAmount = this.cashWithdrawalNew.value.totalAmount;
    //this.cashWithdrawalNew.controls.totalAmount.setValue(this.cashWithdrawalNew.value.totalAmount);
    if (this.cashWithdrawalNew.value.totalAmount != this.cashWithdrawalNew.value.transactionAmount) {
      this.disableSave = true
    }
    else {
      this.disableSave = false
    }
  }

  onKeyUp() {
    this.calculateTotalTransactionAmount();
    this.transAmtInteger = +this.cashWithdrawalNew.get('transactionAmount').value;
  }

  dataByCountryCode(index, item) {
    return item;
  }
  dataByAccountBranch(index, item) {
    return item;
  }
  // getBankInformation(branchCode: any) {
  //   this.apiService.fetchBankInfoByBranchCode(branchCode).subscribe(resp => {
  //     console.log(resp)
  //     this.denomRequired = resp.denomRequired

  //   })
  // }
  waiverChange(val: boolean, i: number) {
    console.log(val);

    this.chargeDetailArray[i].waiver = val;
    this.calculateTotalCharge();
    this.calculateTotalTransactionAmount();
  }

  calculateTotalTransactionAmount() {
    if (this.cashWithdrawalNew.get('exchangeRate').value) {
      this.totalTransactionAmount = (Number(this.cashWithdrawalNew.get('transactionAmount').value) * Number(this.cashWithdrawalNew.get('exchangeRate').value)) + (Number(this.totalChargeAmount));
      this.cashWithdrawalNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount);
    }
    else {
      this.totalTransactionAmount = Number(this.cashWithdrawalNew.get('transactionAmount').value) + Number(this.totalChargeAmount);
      this.cashWithdrawalNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount)
    }


    console.log(this.totalTransactionAmount, this.totalChargeAmount, this.chargeDetailArray);

  }

  calculateTotalCharge() {
    this.totalChargeAmount = 0;
    if (this.chargeDetailArray) {
      for (let i = 0; i < this.chargeDetailArray.length; i++) {
        if (this.chargeDetailArray[i].waiver != true && this.chargeDetailArray[i].amount != null) {
          this.totalChargeAmount = this.totalChargeAmount + this.chargeDetailArray[i].amount;
        }
      }
      // if(this.cashWithdrawalNew.get('exchangeRate').value){
      //   this.totalChargeAmount = this.totalChargeAmount * this.cashWithdrawalNew.get('exchangeRate').value
      // }
    }
    this.cashWithdrawalNew.get('totalChargeAmount').setValue(this.totalChargeAmount)
  }

  successDialog(): void {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: '60%',
      height: '75%',
      backdropClass: 'dialog-bg-trans',
    });
  
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.showTransactionSlip();
    });
  }
  
  showTransactionSlip() {
    this.dashboard.slipEnable = true;
    this.dashboard.transactionDetails = this.cashWithdrawalResponse
    this.dashboard.drawer.open()
  }
}
