import { Component, Input, OnInit } from '@angular/core';
import { ViewChild } from '@angular/core';
import { FormGroup, NgForm } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { MatAccordion } from '@angular/material/expansion';
import { TokenService } from 'app/shared/services/token-service/token.service';
import {OperationsComponent} from 'app/views/others/operations/operations.component';
import {ChequeWithdrawal} from 'app/shared/models/chequeWithdrawal';
import { forkJoin } from 'rxjs';
import { FingerPrintVerificationComponent } from '../finger-print-verification/finger-print-verification.component';
import { MatDialog } from '@angular/material/dialog';
import { ApiService } from 'app/shared/services/api.service';
import { DashboardComponent } from '../../dashboard/dashboard.component';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { TransferComponent } from '../../Dialogs/transfer/transfer.component';
import { currency_symbols } from '../currency-symbols';
import { JwtAuthService } from 'app/shared/services/auth/jwt-auth.service';
import { FingerprintAuthenticationComponent } from '../../Dialogs/fingerprint-authentication/fingerprint-authentication.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { CustomerAuthenticatedDialogComponent } from '../../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component';
import { NgxSpinnerService } from 'ngx-spinner';


@Component({
  selector: 'app-cheque-withdrawal',
  templateUrl: './cheque-withdrawal.component.html',
  styleUrls: ['./cheque-withdrawal.component.scss']
})
export class ChequeWithdrawalComponent implements OnInit {

  @Input("chequeWithdrawalData") chequeWithdrawalData: any;
  @ViewChild(MatAccordion) accordion: MatAccordion;


  title: string;
  operation: string;
  detailsPanelOpened: boolean = false;
  chargeDetailsPanelOpened: boolean = false;
  denominationPanelOpened: boolean = false;
  chequeWithdrawal : ChequeWithdrawal;
  chargeDetailArray : any[] = [];
  dataSource : any;
  currencyImage: string=" ";
  countryValues: string;
  accountTypeId : number;
  pauseTrans: boolean=false;
  currencyType : string = "";
  currencyInfo : any;
  denominationLimit : string;
  showLoader : boolean = false;
  denominationKey:number;
  denominationValue:number;
  tokenArrayHavingRole: any[]=[];
  transferFlag: boolean=false;
  denomCodeArray: any[] = [
    { key: 'count2000', value: 2000 },
    { key: 'count500', value: 500 },
    { key: 'count200', value: 200 },
    { key: 'count100', value: 100 },
    { key: 'count50', value: 50 },
    { key: 'count20', value: 20 },
    { key: 'count10', value: 10 },
    { key: 'count5', value: 5 },
    { key: 'count2', value: 2 },
    { key: 'count1', value: 1 }
    
  ];

  @ViewChild('cwForm') cwForm: NgForm;

  totalAmt: Number;

  cashDepositForm: FormGroup;
  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];
  profileURL: any;
  tokenLists: any;
  denomination: boolean;
  denomRequired: any;
  length: number;
  totalChargeAmount: number=0;
  tellerId: string;
  cancel: boolean=false;
  accountCurrencyImage:string=" ";
  accountCountryValues: any;
  disableSave: boolean = false;
  hold: boolean;
  constructor(
    private tokenService: TokenService,
    private operationsComponent : OperationsComponent,
    public dialog: MatDialog,public dashboard:DashboardComponent,  private ls: LocalStoreService,
    private api:ApiService,
    private jwtService: JwtAuthService,
    private snack:MatSnackBar,
    private spinner: NgxSpinnerService
  ) {

  }

  ngOnChanges() {
    this.chequeWithdrawal = this.chequeWithdrawalData;
    //this.chequeWithdrawal.totalAmount = 0;
  }

  ngOnInit() {
    this.spinner.show();
    this.dashboard.slipEnable = false;
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.denomRequired = localStorage.getItem('denomRequired');
    this.disableSave = this.denomRequired =='yes' ? true : false;    
    this.getCountrynameValues();
    this.getAccountCountrynameValues();
    this.chequeWithdrawal = this.chequeWithdrawalData;
    this.chequeWithdrawal.accountAmount=this.chequeWithdrawal.transactionAmount;
    this.profileURL=this.chequeWithdrawal.profileImage;
    this.currencyInfo = this.chequeWithdrawalData.currencyInfo;
    
    this.chequeWithdrawal.transactionCurrency=this.chequeWithdrawalData.transactionCurrency;
    if(this.chequeWithdrawalData.transactionCurrency){
    this.currencyImage = 'currency-flag-'+this.chequeWithdrawalData.transactionCurrency.toLowerCase();
    }
    if(this.chequeWithdrawalData.accountCurrency){
    this.accountCurrencyImage='currency-flag-'+this.chequeWithdrawalData.accountCurrency.toLowerCase();
    }
    for(let i = 0; i < this.currencyInfo.length; i++){
      if(this.currencyInfo[i].currency == this.chequeWithdrawal.transactionCurrency){
         this.denominationLimit = this.currencyInfo[i].denominations;
      }
   }
   // this.chequeWithdrawal.totalAmount = 0;
    this.getChargeDetails();
    this.getChequeWithdrawalDenomination();
    // this.getBankInformation('KN');
    this.checkPauseTransaction();
    this.getTokensWithPermissions();
    this.lockTokenNumber();
    this.getExchangeRate();
  }


  demArr=[2000,500,200,100,50,20,10,5,2,1];
  getChequeWithdrawalDenomination(){
      for (const key in this.chequeWithdrawal.denomination) {
        this.denomCodeArray.forEach(element => {
          if(this.chequeWithdrawal.denomination[key].denominationKey == element.value){
          // if(this.cashDeposit.denomination[key].denominationKey == 200){
          //     this.cashDeposit.count200 = this.cashDeposit.denomination[key].denominationValue;
          //   }
          var countVal='';
            for (const denom in this.demArr) {
              if (Object.prototype.hasOwnProperty.call(this.demArr, denom)) {
                const denomVal = this.demArr[denom];
                if(this.chequeWithdrawal.denomination[key].denominationKey == denomVal){
                  countVal ='count'+denomVal;
                  this.chequeWithdrawal[countVal] = this.chequeWithdrawal.denomination[key].denominationValue;
                }
              }
            }
          
           }
        });
      }
  }
  checkPauseTransaction() {
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.tokenService.checkPauseTransaction(this.tellerId).subscribe(resp=>{
      const isPause=resp.data.some(d=>d.tokenNumber === this.chequeWithdrawal.tokenNo)
      if(isPause === true)
           {
          this.pauseTrans=true;
          }      
        })
      }
      getAccountCountrynameValues() {
        this.api.getCountryValues().subscribe((allCountryValuesForAccount : any)=> {
          this.accountCountryValues = allCountryValuesForAccount;
          });
      }
    
  getChargeDetails(){
    if(this.chequeWithdrawal.accountType == "Savings"){
      this.accountTypeId = 1;
    }
    else if(this.chequeWithdrawal.accountType == "Current"){
      this.accountTypeId = 2;
    }
    else if(this.chequeWithdrawal.accountType == "Joint"){
      this.accountTypeId = 3;
    }
    else{
      this.accountTypeId = null;
    }
    this.tokenService.getChargeDetails(this.chequeWithdrawal.accountNumber).subscribe((resp) => {
      if(resp){
      this.chargeDetailArray = resp;
      for (let index = 0; index < this.chargeDetailArray.length; index++) {
        if(this.chargeDetailArray[0].waiver==false || this.chargeDetailArray[0].waiver==null){
         
          this.totalChargeAmount += this.chargeDetailArray[index].amount
        
          }
      }
    }
      this.chequeWithdrawal.accountAmount=this.chequeWithdrawal.accountAmount+this.totalChargeAmount
      this.chequeWithdrawal.totalChargeAmount=this.totalChargeAmount
        this.dataSource = new MatTableDataSource(this.chargeDetailArray);
        this.length=this.dataSource.filteredData.length 
    });
  }
  pauseTransaction(){
    this.pauseTrans = !this.pauseTrans;
    this.tokenService.pauseTransaction(this.chequeWithdrawal.tokenNo,this.pauseTrans).subscribe(resp=>{
      if(resp.message!="Status updated successfully"){
        this.pauseTrans = !this.pauseTrans;
      }
      })
  }

  waiverChange(checked: boolean, i: number) {
    this.chargeDetailArray[i].waiver = checked;
    if(!checked){
      this.chequeWithdrawal.accountAmount=this.chequeWithdrawal.accountAmount+this.chargeDetailArray[i].amount;
      this.totalChargeAmount += this.chargeDetailArray[i].amount;
      this.chequeWithdrawal.totalChargeAmount=this.totalChargeAmount
      }
      else{
        this.chequeWithdrawal.accountAmount-=this.chargeDetailArray[i].amount
        this.totalChargeAmount -= this.chargeDetailArray[i].amount;
        this.chequeWithdrawal.totalChargeAmount=this.totalChargeAmount
      }
  }

  onclickofConfirm(cwForm: NgForm) {

    let denominationData: any = {};
    let denominationDataArray: any[] = [];

    for (let i = 0; i < this.denomCodeArray.length; i++) {
      if (cwForm.value[this.denomCodeArray[i].key]) {
        denominationData.transactionId = this.chequeWithdrawalData.id;
        denominationData.denominationKey = this.denomCodeArray[i].value;
        denominationData.denominationValue = +cwForm.value[this.denomCodeArray[i].key];
        denominationData.currency = "INR";
        // denominationData.amountAvailiable = 15200 - (cwForm.value[this.denomCodeArray[i].key] * this.denomCodeArray[i].value);
        denominationDataArray.push(denominationData);
        denominationData = {};
      }
    }

    delete cwForm.value.count2000;
    delete cwForm.value.count500;
    delete cwForm.value.count200;
    delete cwForm.value.count100;
    delete cwForm.value.count50;
    delete cwForm.value.count20;
    delete cwForm.value.count10;
    delete cwForm.value.count5;
    delete cwForm.value.count2;

    this.chequeWithdrawal = cwForm.value;
    this.chequeWithdrawal.denomination = denominationDataArray;
    this.chequeWithdrawal.transactionId = this.chequeWithdrawalData.transactionId;
    this.chequeWithdrawal.tellerId = this.chequeWithdrawalData.tellerId;
    this.chequeWithdrawal.holderName=this.chequeWithdrawalData.holderName;
    this.chequeWithdrawal.tokenNo=this.chequeWithdrawalData.tokenNo;
    this.chequeWithdrawal.customerId=this.chequeWithdrawalData.customerId;
    if(this.pauseTrans === true){
  
          
  
          const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
            width: '60%',
            height: '70%',
            backdropClass: 'dialog-bg-trans',
          });
      
          dialogRef.afterClosed().subscribe(result => {
            if(result != 'close'){        
                this.showLoader = true;
                this.tokenService.saveCashDepositData(this.chequeWithdrawal).subscribe((resp1) => {
                  if(resp1){
                    this.showLoader = false;
                    this.operationsComponent.closeDrawer();
                    this.dashboard.getCounterDashBoard();
                    this.dashboard.getDashboardRecords();
                    this.showTransactionSlip(resp1);
                  }
                 
                },(err)=>{
                  this.snack.open(`Internal Server Error`, 'OK', {
                    duration: 2000,
                    verticalPosition: 'top',
                    horizontalPosition: 'right'
                  })
                  this.showLoader=false;
                })
              
            }
            else{
              ;
            }
          });
  
      return;
     }
     if(this.transferFlag === false || this.cancel === true){
      this.tokenLists=this.ls.getItem('TOKENLIST')
      this.showLoader = false;
     
      if(this.tokenLists!=null){
      if(this.tokenLists.length >1 || this.tokenLists.length === 1){
        for(let i=0 ; i< this.tokenLists.length ; i++ ){
          if(this.chequeWithdrawalData.tokenNo == this.tokenLists[i].tokenNumber)
          {
            this.tokenLists.splice(i, 1);
            break;
          }
        }
        localStorage.removeItem('TOKENLIST');
        localStorage.setItem('TOKENLIST',JSON.stringify(this.tokenLists));
        this.tokenLists=this.ls.getItem('TOKENLIST');
       if(this.tokenLists.length >= 1){
        this.operationsComponent.closeDrawer();
        this.dashboard.pickingChildTokenNumber(this.tokenLists[0].tokenNumber);
       }
  
        }
    
      }
      if(this.tokenLists == null || this.tokenLists.length === undefined || this.tokenLists.length=== 0){
        this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
          (data: any) => {
            this.tokenArrayHavingRole = data;
            if(this.tokenArrayHavingRole.length > 0){
              this.dashboard.pickingChildTokenNumber(this.tokenArrayHavingRole[0].tokenNumber);
            }
            else if(this.tokenArrayHavingRole.length === 0){
              this.operationsComponent.closeDrawer();
              this.dashboard.getCounterDashBoard();
              this.dashboard.getDashboardRecords();
            }
          },
          (error: any) => {
          }
        );
  
            }

     }
else{
  this.showLoader = true;
 
    const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
      width: '60%',
      height: '70%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      this.tokenService.saveCashDepositData(this.chequeWithdrawal).subscribe((resp1) => {
        if(resp1){
          this.showLoader = false;
          this.operationsComponent.closeDrawer();
          this.dashboard.getCounterDashBoard();
          this.dashboard.getDashboardRecords();
          this.showTransactionSlip(resp1);
       
        }
      },(err)=>{
        this.snack.open(`Internal Server Error`, 'OK', {
          duration: 2000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.showLoader=false;
      });
    });
  
 


}

  }


  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }

  cancelOperation(){
    this.cancel=true;
    this.tokenService.cancelOrLock(this.chequeWithdrawal.tokenNo,this.cancel,this.tellerId).subscribe(resp=>{
      console.log(resp);
 },err=>{

 })
    this.cwForm.reset();
    this.accordion.closeAll();
    this.dashboard.getCounterDashBoard();
    this.dashboard.getDashboardRecords();
    this.operationsComponent.closeDrawer();
  }


  totalAmountValue(cwForm: NgForm) {
    this.denomination=true;
    this.chequeWithdrawal.totalAmount =
      ((cwForm.value.count2000 ? cwForm.value.count2000 * 2000 : 0) +
        (cwForm.value.count500 ? cwForm.value.count500 * 500 : 0) +
        (cwForm.value.count200 ? cwForm.value.count200 * 200 : 0) +
        (cwForm.value.count100 ? cwForm.value.count100 * 100 : 0) +
        (cwForm.value.count50 ? cwForm.value.count50 * 50 : 0) +
        (cwForm.value.count20 ? cwForm.value.count20 * 20 : 0) +
        (cwForm.value.count10 ? cwForm.value.count10 * 10 : 0) +
        (cwForm.value.count5 ? cwForm.value.count5 * 5 : 0) +
        (cwForm.value.count2 ? cwForm.value.count2 * 2 : 0) +
        (cwForm.value.count1 ? cwForm.value.count1 * 1 : 0)
        );
        if(this.chequeWithdrawal.totalAmount != this.chequeWithdrawal.transactionAmount){
          this.disableSave = true;
        }
        else{
          this.disableSave = false;
        }
  }


  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  openDialog(): void {
    var name='cat';
     let dialogRef = this.dialog.open(FingerPrintVerificationComponent, {
       width: '500px',
       height:'500px',
       data: { name: name }
     });
 
     dialogRef.afterClosed().subscribe(result => {
       name = result;
     });
   }



   _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  onCountryChange(country) {
    this.chequeWithdrawal.exchangeRate=0;
    this.chequeWithdrawal.accountAmount=this.chequeWithdrawal.transactionAmount+this.chequeWithdrawal.totalChargeAmount;
    let currency = country.currencyCode;
    this.currencyImage = 'currency-flag-'+currency.toLowerCase();
    this.getExchangeRate();
  }
  addCurrencyImage(currency) {
    return currency.toLowerCase();
  } 
  getCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValues : any)=> {
      this.countryValues = allCountryValues;
      });
  }
  dataByCountryCode(index,item){
    return item;
  }

  // getBankInformation(branchCode:any){
  //   this.api.fetchBankInfoByBranchCode(branchCode).subscribe(resp => {
  //     this.denomRequired = resp.denomRequired
      
  //   }) 
  // }
  transfer(){
    const dialogRef = this.dialog.open(TransferComponent, {
      width: '60%',
      height: '83%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
      data: {
        tokenNumber:this.chequeWithdrawal.tokenNo
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
   
    });
  }
  getTokensWithPermissions(){
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
      (data: any) => {
        this.tokenArrayHavingRole = data;
      if(this.tokenArrayHavingRole.length >= 1){
       this.transferFlag = data.some(d=>d.tokenNumber === this.chequeWithdrawal.tokenNo);
       this.spinner.hide();
      }
      },
      (error: any) => {
        this.spinner.hide();
      }
    );
   }
   successDialog(): void {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: '60%',
      height: '75%',
      backdropClass: 'dialog-bg-trans',
      // disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      // this._document.defaultView.location.reload();
    });
   }
  lockTokenNumber(){
   
    this.tokenService.cancelOrLock(this.chequeWithdrawal.tokenNo,this.cancel,this.tellerId).subscribe(resp=>{
         console.log(resp);
    },err=>{
    })
  }
  onAccountCountryChange(country) {

    let currency = country.currencyCode;
    this.accountCurrencyImage = 'currency-flag-'+currency.toLowerCase();
  }
  dataByAccountCountryValues(countryVal)
  {
    return countryVal.currencyCode;
  }
  showTransactionSlip(resp) {
    this.dashboard.slipEnable = true;
    this.dashboard.transactionDetails = resp;
    this.dashboard.transactionDetails.pauseTrans=this.pauseTrans
    this.dashboard.getCounterDashBoard();
    this.dashboard.drawer.open()
  }
  cancelTransactionSlip(chequeWithdrawal){
    this.dashboard.slipEnable = false;
    var user = this.jwtService.getUser();
    if (user) {
      this.tellerId = user.userId;
    }
    this.dashboard.slipEnable = false;
    console.log("canceling transaction slip from cheque withdrwal")
    if(!this.dashboard.slipEnable && !chequeWithdrawal.pauseTrans){
      this.tokenLists=this.ls.getItem('TOKENLIST');
      if(this.tokenLists!=null){
        if(this.tokenLists.length >1 || this.tokenLists.length === 1){
          for(let i=0 ; i< this.tokenLists.length ; i++ ){
            if(chequeWithdrawal.tokenNo == this.tokenLists[i].tokenNumber)
            {
              this.tokenLists.splice(i, 1);
              break;
            }
          }
          localStorage.removeItem('TOKENLIST');
          localStorage.setItem('TOKENLIST',JSON.stringify(this.tokenLists));
          this.tokenLists=this.ls.getItem('TOKENLIST');
         if(this.tokenLists.length >= 1){
          this.dashboard.pickingChildTokenNumber(this.tokenLists[0].tokenNumber);
         }
          }
      
        }
        if(this.tokenLists == null || this.tokenLists.length === undefined || this.tokenLists.length=== 0){
          this.tokenService.getCounterDashBoard(this.tellerId).subscribe(
            (data: any) => {
              this.tokenArrayHavingRole = data;
              if(this.tokenArrayHavingRole.length > 0){
                this.dashboard.pickingChildTokenNumber(this.tokenArrayHavingRole[0].tokenNumber);
              }
              else if(this.tokenArrayHavingRole.length === 0){
                this.dashboard.drawer.close()
                return;
              }
            },
            (error: any) => {
            }
          );
    
              }
          }
          else{
            this.dashboard.drawer.close()
                return;
          }
  }
  getExchangeRate(){
    if(this.chequeWithdrawal.transactionCurrency != this.chequeWithdrawal.accountCurrency){
      this.api.getExchangeRateforCurrency(this.chequeWithdrawal.transactionCurrency,this.chequeWithdrawal.accountCurrency).subscribe(res => {
        if(res[res.length-1].sellRate){
        this.chequeWithdrawal.exchangeRate=res[res.length-1].sellRate;
        this.chequeWithdrawal.accountAmount=(this.chequeWithdrawal.transactionAmount * res[res.length-1].sellRate)+this.chequeWithdrawal.totalChargeAmount;
        }
      })
    }
  }
  holdToken(cwForm: NgForm){
    this.cancel=true;
    this.tokenService.cancelOrLock(this.chequeWithdrawal.tokenNo,this.cancel,this.tellerId).subscribe(resp=>{
      console.log(resp);
  },err=>{
  if(err.error.text=='Updated Successfully'){
    this.hold=true;
  this.snack.open('Token holded Successfully', 'OK', {
    duration: 2000,
    verticalPosition: 'top',
    horizontalPosition: 'right'
  });
  setTimeout(()=>{
    this.onclickofConfirm(cwForm);
  },2000)
  }
  })
  }
}
 