import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { TokenService } from 'app/shared/services/token-service/token.service';

@Component({
  selector: 'app-new-customer-onboarding',
  templateUrl: './new-customer-onboarding.component.html',
  styleUrls: ['./new-customer-onboarding.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class NewCustomerOnboardingComponent implements OnInit {

  selectedIndex: number = 0;
  imgUrl: string | ArrayBuffer;
  profilePicture: any;
  proofDocForm: FormGroup;
  userAddressForm: FormGroup;
  otherDocForm: FormGroup;
  proofDoc_array: FormArray;
  otherDoc_array: FormArray;
  userAddress_array: FormArray;
  addNewProof: boolean = false;
  arrayPrefix: string[] = [];
  arrayGender: string[] = [];
  arrayMaritalStatus: string[] = [];
  arrayCountry: string[] = [];
  arrayResidence: string[] = [];
  arrayDocumentType: string[] = [];
  arrayEmploymentType: string[] = [];
  arraykycStatus:string[]=['NOT_VERIFIED', 'INITATED', 'UNDER_PROCESS', 'VERIFIED']

  arrayEmployerName: string[] = [];
  otherDocumentArray: any[] = [];
  proofDocumentArray: any[] = [];



  @ViewChild('ncForm') ncForm: NgForm;



  constructor(private fb: FormBuilder, private apiService: TokenService) { }

  ngOnInit(): void {
    this.proofDocForm = this.fb.group({
      proofDoc_array: this.fb.array([])
    });
    this.otherDocForm = this.fb.group({
      otherDoc_array: this.fb.array([])
    })
    this.userAddressForm = this.fb.group({
      userAddress_array: this.fb.array([])
    })
    this.imgUrl = "assets/images/profile-Picture.png";
    this.addproofDocItem();
    this.addOtherDocItem();
    this.addMailingAddress("No");
    this.getPrefixValue("prefix");
    this.getGenderValue("gender");
    this.getMaritalStatus("maritial_Status");
    this.getCountryName("country");
    this.getResidenceDetails("residence");
    this.getDocumentType("document_type");
    this.getEmploymentType("employment_type");
    this.getEmployerName("employer_name");
  }


  createDocItem() {
    return this.fb.group({
      documentType: [''],
      documentName: [''],
      fileData: [''],
    })
  }

  createUserAddress() {
    return this.fb.group({
      address1: [''],
      address2: [''],
      city: [''],
      zipCode: [''],
      country: [''],
      residenceType: [''],
      addressType: [''],
      customerId: ['']
    })
  }

  nextStep(tabIndex: number) {
    if (this.selectedIndex < 3) {
      this.selectedIndex = tabIndex + 1;
    }
  }

  previousStep(tabIndex: number) {
    if (this.selectedIndex > 0) {
      this.selectedIndex = tabIndex - 1;
    }
  }

  getPrefixValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayPrefix = data;
    });
  }
  getGenderValue(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayGender = data;
    });
  }
  getMaritalStatus(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayMaritalStatus = data;
    });
  }
  getCountryName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayCountry = data;
    });
  }
  getResidenceDetails(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayResidence = data;
    });
  }
  getDocumentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayDocumentType = data;
    });
  }
  getEmploymentType(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayEmploymentType = data;
    });
  }
  getEmployerName(key: string) {
    this.apiService.getDropDownValues(key).subscribe((data) => {
      this.arrayEmployerName = data;
    });
  }

  onclickofFinish(ncForm: NgForm) {
    // "kycReference":"kyc_8041","kycStatus":"UNDER_PROCESS"
    let obj_test: any = {};
    obj_test = ncForm.value;
    obj_test.userAddress = this.userAddress_array.value;
    let customerAdditionalInfo: any = {};
    customerAdditionalInfo.employmentType = obj_test.employmentType;
    customerAdditionalInfo.employerName = obj_test.employerName;
    customerAdditionalInfo.salary = obj_test.salary;
    customerAdditionalInfo.kycReference = obj_test.kycReference;
    customerAdditionalInfo.kycStatus = obj_test.kycStatus;
    obj_test.customerAdditionalInfo = customerAdditionalInfo;
    delete obj_test.employmentType;
    delete obj_test.employerName;
    delete obj_test.salary;
    delete obj_test.kycReference;
    delete obj_test.kycStatus;

    this.apiService.insertNewUserOnboarding(obj_test).subscribe((result) => {
      if (result) {
        for (let i = 0; i < this.otherDoc_array.value.length; i++) {
          let documentToBeUploaded: any = {};
          documentToBeUploaded.documentName = this.otherDoc_array.value[i].documentName;
          documentToBeUploaded.documentType = +this.otherDoc_array.value[i].documentType;
          documentToBeUploaded.fileType = this.otherDocumentArray[i].type;
          documentToBeUploaded.fileName = this.otherDocumentArray[i].name;
          documentToBeUploaded.customerId = result.customerId;
          const uploadData = new FormData();
          uploadData.append('data', JSON.stringify(documentToBeUploaded));
          uploadData.append('file', this.otherDocumentArray[i]);
          this.apiService.uploadDocument(uploadData).subscribe((resp) => {
          });
        }
        for (let i = 0; i < this.proofDoc_array.value.length; i++) {
          let documentToBeUploaded: any = {};
          documentToBeUploaded.documentName = this.proofDoc_array.value[i].documentName;
          documentToBeUploaded.documentType = +this.proofDoc_array.value[i].documentType;
          documentToBeUploaded.fileType = this.proofDocumentArray[i].type;
          documentToBeUploaded.fileName = this.proofDocumentArray[i].name;
          documentToBeUploaded.customerId = result.customerId;
          const uploadData = new FormData();
          uploadData.append('data', JSON.stringify(documentToBeUploaded));
          uploadData.append('file', this.proofDocumentArray[i]);
          this.apiService.uploadDocument(uploadData).subscribe((op) => {
          });
        }

        let profileUpload: any = {};
        profileUpload.documentName = "Profile";
        profileUpload.documentType = 0;
        profileUpload.fileType = this.profilePicture.type;
        profileUpload.fileName = this.profilePicture.name;
        profileUpload.customerId = result.customerId;
        const uploadData = new FormData();
        uploadData.append('data', JSON.stringify(profileUpload));
        uploadData.append('file', this.profilePicture);
        this.apiService.uploadDocument(uploadData).subscribe((response) => {
        });
        
        alert("Saved successfully!");
      }
      else {
      }
    });
  }

  public profileChange(event) {
    this.profilePicture = event.target.files[0];
    let reader = new FileReader();
    reader.readAsDataURL(event.target.files[0]);
    reader.onload = (event2) => {
      this.imgUrl = reader.result;
    };
  }

  addproofDocItem() {
    this.proofDoc_array = this.proofDocForm.get('proofDoc_array') as FormArray;
    this.proofDoc_array.push(this.createDocItem());
  }

  addOtherDocItem() {
    this.otherDoc_array = this.otherDocForm.get('otherDoc_array') as FormArray;
    this.otherDoc_array.push(this.createDocItem());
  }

  addMailingAddress(sameAddress: any) {
    this.userAddress_array = this.userAddressForm.get('userAddress_array') as FormArray;
    if (sameAddress == "No") {
      this.userAddress_array.push(this.createUserAddress());
    }
    else if (sameAddress == "Yes") {
      this.userAddress_array.removeAt(this.userAddress_array.length - 1);
    }
  }

  onOtherDocSelect(files: FileList) {
    this.otherDocumentArray.push(files.item(0));
  }

  onProofDocSelect(files: FileList) {
    this.proofDocumentArray.push(files.item(0));
  }

  trackByPrefix(prefix){
    return prefix;
  }


  trackByGender(gender){
    return gender;
  }
  trackByMartialStatus(status){
    return status;
  }
  trackByCountry(country){
    return country;
  }
  trackByUserAddress(report){
    return report;
  }
  trackByResidence(residence){
    return residence;
  }
  trackByProof(report){
    return report;
  }
  trackByDoc(type){
    return type;
  }
  trackByEmpType(empType){
    return empType;
  }
  v
  trackByEmpName(emprName){
    return emprName;
  }
  trackByKycStatus(kyc){
    return kyc;
  }
  trackByOtherDoc(report){
    return report;
  }
  trackByDocType(doctypeValue){
    return doctypeValue;
  }
}
