import { ValueConverter } from '@angular/compiler/src/render3/view/template';
import { Component, Inject, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatAccordion } from '@angular/material/expansion';
import { MatDrawer } from '@angular/material/sidenav';
import { TransferState } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { OperationsComponent } from '../../operations/operations.component';
import { CashWithdrawalComponent } from '../../tokenOperations/cash-withdrawal/cash-withdrawal.component';
import { TransferTokenComponent } from '../transfer-token/transfer-token.component';

@Component({
  selector: 'app-transfer',
  templateUrl: './transfer.component.html',
  styleUrls: ['./transfer.component.scss']
})
export class TransferComponent implements OnInit {
  @Input("cashWithdrawalData") cashWithdrawalData: any;
  @ViewChild(MatAccordion) accordion: MatAccordion;
  @ViewChild('drawer', { static: true }) public drawer!: MatDrawer;
  public transferForm: FormGroup;
  data: number[]=[];
  tokenNumber: any;
  loggedInTellerId: any;
  constructor(private router: Router,  private apiService: ApiService,  private fb: FormBuilder,
    public dialogRef: MatDialogRef<TransferComponent>,  public dialogRef1: MatDialogRef<CashWithdrawalComponent>, public dialog: MatDialog,private operationsComponent: OperationsComponent,
    @Inject(MAT_DIALOG_DATA) private dataTeller: any,private ls:LocalStoreService) {
      this.tokenNumber=dataTeller.tokenNumber
     }

  ngOnInit(): void {
    this.loggedInTellerId = this.ls.getItem('tellerid');
    this.fetchUserInfo();
    this.transferForm = this.fb.group({
      tellerName: ['', Validators.required], //dropdown
      transferDesc: ['', Validators.required],
      tokenNumber:[''],
      tellerId:['']
    });
  }
  fetchUserInfo(){
    this.apiService.getUserInfo().subscribe((data: any) => {
this.data=data;
console.log(this.data)
    });
  }
  Close(): void {
    this.dialogRef.close();
    this.router.navigate(['/others/dashboard']);
  }

  submit(){
    var name=this.transferForm.value.tellerName
    this.transferForm.get('tokenNumber').setValue(this.tokenNumber);
    delete this.transferForm.value.tellerName
     console.log(this.transferForm.value)
     this.apiService.transferToken(this.transferForm.value,this.loggedInTellerId).subscribe(resp=>{
     if(resp){
          this.dialogRef.close();
    this.router.navigate(['/others/dashboard']);
     let dialogRef = this.dialog.open(TransferTokenComponent, {
      width: '60%',
      height: '75%',
       data: { name: name }
     });
 
     dialogRef.afterClosed().subscribe(result => {
       name = result;
     });
     }
     })
  }
  ConvertStringToNumber(input: string) {
    var numeric = Number(input);
    return numeric;
}
onSelectTellerId(value){
 this.transferForm.get('tellerId').setValue(value.userId);
  console.log(value)
}
}
