import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ApiService } from 'app/shared/services/api.service';
import { DialogService } from '../../Dialogs/dialog.service';

@Component({
  selector: 'app-token-generation',
  templateUrl: './token-generation.component.html',
  styleUrls: ['./token-generation.component.scss']
})
export class TokenGenerationComponent implements OnInit {
  @ViewChild('cancelDrawer') cancelDrawer;
  titleMessage = "Your token has been successfuly generated";
  contentMessage = "Your token number is given below";
  tokenNum: string;
  estimatedTime = "Estimated wait time";
  waitingTime: string;
  phoneNumber: string;

  constructor(private router: Router,private apiService:ApiService,
    private dialogService : DialogService,
    public dialogRef: MatDialogRef<TokenGenerationComponent>,private fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data:any
  ) { 
    
  }

  ngOnInit(): void {
    this.phoneNumber = localStorage.getItem("phonenumFromLogin");
    this.tokenNum = localStorage.getItem("tokenNumer");
    this.waitingTime = localStorage.getItem("estimatedTiming");
  }
  goToClose(){
    this.dialogRef.close(); 
  }
  goToScanner(){
    this.dialogRef.close();
    this.router.navigateByUrl('/others/dashboard');
  }

  OnClickOfLogout() {
    this.dialogRef.close();
    let el: HTMLElement = document.getElementById('cancelDrawer') as HTMLElement;
    el.click();
    //this.operationsComponent.closeDrawer();
    //this.dialogService.logoutDialog();
    this.router.navigateByUrl('/others/dashboard');
  }

}
