addAccClass() {
  let doc=new DocData();
  this.docData.push(doc);


}

initGroup() {
  console.log("enter inside add")
  let rows = this.arrayForm.get('rows') as FormArray;
  rows.push(this.fb.group({
    documentName: ['', [Validators.required]],
    documentType: ['', [Validators.required]],
  }))
}
getDoc() {
  this.indexs="prefix";
  this.dropVariableDoc='document_type';
  this.docName="document_name";
  this.bioName="biometrics_Name";
  this.bioType="biometrics_Type";
  this.gender="gender";
  this.resi="residence";
  this.marr="maritial_Status";
this.coun="country";
this.nation="nationality";
  this.tokenService.getDropDownValues(this.dropVariableDoc).subscribe(data => {
    this.Documents = data;
  });
  this.tokenService.getDropDownValues(this.docName).subscribe(resp => {
    this.DocumentsName = resp;
  });
  this.tokenService.getDropDownValues(this.bioName).subscribe(response => {
    this.biometrics_Name = response;
  });
  this.tokenService.getDropDownValues(this.bioType).subscribe(responseData => {
    this.biometrics_Type = responseData;
  });
  this.tokenService.getDropDownValues(this.coun).subscribe(responseData => {
    this.countrys = responseData;
  });
  this.tokenService.getDropDownValues(this.gender).subscribe(responseDatas => {
    this.genders = responseDatas;
  });
  this.tokenService.getDropDownValues(this.resi).subscribe(responses => {
    this.Residences = responses;
  });
  this.tokenService.getDropDownValues(this.marr).subscribe(respo => {
    this.MarritialStatus = respo;
  });
  this.tokenService.getDropDownValues(this.indexs).subscribe(re => {
    this.index = re;
  });
  this.tokenService.getDropDownValues(this.nation).subscribe(datas => {
    this.nationality = datas;
  });
}
