import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-transfer-token',
  templateUrl: './transfer-token.component.html',
  styleUrls: ['./transfer-token.component.scss']
})
export class TransferTokenComponent implements OnInit {
  titleMessage: string = "Token Successfully Transferred";
  contentMessage: string = "To @Srinivasan";
  name: any;

  constructor(
    private router: Router,
    public dialogRef: MatDialogRef<TransferTokenComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) {
    this.name=data.name;
   }

  ngOnInit() {
    
  }

  Close(): void {
    this.dialogRef.close();
    window.location.reload();
  }


}