import { Component, OnInit, Input } from '@angular/core';
import * as moment from 'moment';
import { DatePickerDetails } from './datePickerDetails';

@Component({
  selector: 'app-demo',
  templateUrl: './demo.component.html',
  styleUrls: ['./demo.component.scss']
})
export class DemoComponent implements OnInit {
  dateOfoutPut: string;
  resettingBtn:any;
  defaultValue:any;
  datePickerData=new DatePickerDetails();

  constructor() { }

  ngOnInit(): void {
    this.datePickerData.datePickerLabel="Select Date";
    this.datePickerData.errorMsg="Date is Mandatory";
    this.datePickerData.defaultValue=new Date();
    this.datePickerData.mandatory=true;
    this.datePickerData.dateFormat= 'DD-MM-YYYY';
    
  }
  recivingDate(datePickerDate)
  {
    console.log("app",datePickerDate);
    this.datePickerData.resetData=datePickerDate;
    const dateformat = moment(datePickerDate).format('DD-MM-YYYY');
    this.dateOfoutPut=dateformat;
    this.defaultValue=this.dateOfoutPut;
  }
  onReset()
  {
    // this.resettingBtn=null;
    this.datePickerData.defaultValue=null
    this.dateOfoutPut=null;
  }
}
