import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { OperationsComponent } from '../../operations/operations.component';
import { FormGroup, Validators, FormBuilder, NgForm, AbstractControl } from '@angular/forms';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DialogService } from '../../Dialogs/dialog.service';
import { ApiService } from 'app/shared/services/api.service';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog } from '@angular/material/dialog';
import { DashboardComponent } from '../../dashboard/dashboard.component';
import { CustomerAuthenticatedDialogComponent } from '../../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component';

@Component({
  selector: 'app-new-cash-deposit',
  templateUrl: './new-cash-deposit.component.html',
  styleUrls: ['./new-cash-deposit.component.scss']
})
export class NewCashDepositComponent implements OnInit {
  @ViewChild('drawer', { static: true }) public drawer!: MatDrawer;
  @ViewChild('cdForm') cdForm: NgForm;
  loading: boolean;

  countryValues: any;
  user: any;
  accountBal: any;
  holderName: any;
  branchName:any ;
  arrayAccountBranch: any;
  branchNameFromLogin: string;
  accountResp: any;
  invalidAccNo : boolean = false;
  disableSave: boolean;
  invalidAccNoMsg : any;
  trxnAmtFlag: boolean=false;
  denomCodeArray: any[] = [
    { key: 'count2000', value: 2000 },
    { key: 'count500', value: 500 },
    { key: 'count200', value: 200 },
    { key: 'count100', value: 100 },
    { key: 'count50', value: 50 },
    { key: 'count20', value: 20 },
    { key: 'count10', value: 10 },
    { key: 'count5', value: 5 },
    { key: 'count2', value: 2 },
    { key: 'count1', value: 1 }
  ];
  a: any;
  cashDeposit : any = 'Cash Deposit';
  cashDepositFormNew: FormGroup;
  transamount: any;
  totalAmount: any;
  transAmt: any;
  currencyImage: any = "";
  cashDepositResponse: any;
  tokenNum: any;
  estimatedTime: any;
  enableContinue: boolean;
  tellerId: string;
  denomination:any[]=[];
  transactionId: number;
  enableDenomination:boolean=false;
  denomRequired: any;
  chargeDetailArray: any;
  dataSource: any;
  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];
  totalChargeAmount: number=0;
  totalTransactionAmount: number=0;
  exchangeRate: any;
  transValue: number;
  validPattern = "^(?![a-zA-Z\s]+$)[a-zA-Z0-9]+$";

  constructor(private operationsComponent: OperationsComponent,
    private dialogService : DialogService,
    private snack: MatSnackBar,
    private ls: LocalStoreService,
    public dialog: MatDialog,
    private api: TokenService,
    private apiService: ApiService,
    private fb: FormBuilder,
    private dashboard:DashboardComponent) { }

  Onlynumber(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  ngOnInit(): void {
    console.log('check')
    this.branchNameFromLogin=localStorage.getItem('branchName');
    this.tellerId = localStorage.getItem('tellerid');
    this.denomRequired = localStorage.getItem('denomRequired');
    this.disableSave = this.denomRequired =='yes' ? true : false    
    if(this.branchNameFromLogin.includes('null')){
     this.branchName='Kasturi Nagar';
    // this.arrayAccountBranch='Kasturi Nagar';
    //  this.arrayAccountBranch= [{'branchName':'Kasturi Nagar'}];
    }else{
      this.branchName=this.branchNameFromLogin;
    }
    this.cashDepositFormNew = this.fb.group({

      accountNumber: ['', [Validators.required,Validators.minLength(4),Validators.maxLength(15),Validators.pattern(this.validPattern)]],
      accountType: ['', [Validators.required]],
      holderName: ['', [Validators.required]],
      accountCurrency: ['', [Validators.required]],
      transactionCurrency: ['', [Validators.required]],
      transactionAmount: ['', [Validators.required, Validators.min(.1)]],
      productCode: ['CHD'],
      tokenOrigin: ['Teller'],
      accountBranch: ['', [Validators.required]],
      customerId:[''],
      transactionBranch:[''],
      tellerId:[''],
      transactionId: [''],
      exchangeRate:[''],
      totalTransactionAmount:[''],
      totalChargeAmount:[''],
      narrative:['']

    });

    //this.cashDepositFormNew.get('accountBranch').setValue(this.branchName);
    this.cashDepositFormNew.get('transactionBranch').setValue(this.branchName);
    this.user = this.ls.getItem('ICUST_USER');
   
    // this.cashDepositFormNew.controls.holderName.setValue(this.user.tellerName);
    this.getCountrynameValues();
    this.getAccountBranch();
    //  this.fetchingHolderDetailsForCashDeposit();
  }
  cancelOperation() {
    this.dashboard.serviceEnable=false;
    this.operationsComponent.closeDrawer();
    this.accountBal = null;
      this.cashDepositFormNew.reset();
   
  }
  getCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValues: any) => {
      this.countryValues = allCountryValues;
     
    });
    // this.getBankInformation('KN');
  }
  onCashDeposit() {
    var withoutComma=this.cashDepositFormNew.get('transactionAmount').value.replace(/\,/g,'')
    let denominationData: any = {};
    let denominationDataArray: any[] = [];
if(this.cdForm){
    for (let i = 0; i < this.denomCodeArray.length; i++) {
      if (this.cdForm.value[this.denomCodeArray[i].key]) {
        //denominationData.transactionId = this.cashWithdrawal1.id;
        denominationData.transactionId = this.cashDepositFormNew.value.transactionId,
        denominationData.denominationKey = this.denomCodeArray[i].value;
        denominationData.denominationValue = +this.cdForm.value[this.denomCodeArray[i].key];
        denominationData.currency = this.cashDepositFormNew.value.transactionCurrency;
        // denominationData.amountAvailiable = 15200 - (cwForm.value[this.denomCodeArray[i].key] * this.denomCodeArray[i].value);
        denominationDataArray.push(denominationData);
        denominationData = {};
      }   
    }      
      
    delete this.cdForm.value.count2000;
    delete this.cdForm.value.count500;
    delete this.cdForm.value.count200;
    delete this.cdForm.value.count100;
    delete this.cdForm.value.count50;
    delete this.cdForm.value.count20;
    delete this.cdForm.value.count10;
    delete this.cdForm.value.count5;
    delete this.cdForm.value.count2;

    console.log('denomData',denominationDataArray)
    var denomination_Ob = [{
    //  transactionId: this.cashWithdrawal1.id,
      transactionId: this.cashDepositFormNew.value.transactionId,
      denominationKey:denominationData.denominationKey,
      denominationValue:denominationData.denominationValue,
      currency:this.cashDepositFormNew.value.transactionCurrency
   }];
   denomination_Ob=denominationDataArray;
   denominationDataArray=this.denomination;
  }
    let custId=this.accountResp.customerId;
    
    this.cashDepositFormNew.get('customerId').setValue(+custId);
    this.cashDepositFormNew.get('tellerId').setValue(this.tellerId);
    this.cashDepositFormNew.get('productCode').setValue('CHD');
    this.cashDepositFormNew.value.denomination = denomination_Ob;
    this.cashDepositFormNew.value.transactionId = this.transactionId;
    this.cashDepositFormNew.value.transactionAmount=withoutComma;
    this.api.saveofNewCashDeposit(this.cashDepositFormNew.value).subscribe(CashDeposiResp => {
      
      if (CashDeposiResp) {
        this.cashDepositFormNew.value.transactionId = this.transactionId;
        this.transactionId = CashDeposiResp.transactionId;
        this.accountBal = null;
        this.holderName ="";
        this.cashDepositResponse = CashDeposiResp;
        this.cashDepositResponse.transactionType = 'Cash Deposit'
        this.tokenNum = this.cashDepositResponse.tokenNumber;
        this.estimatedTime = this.cashDepositResponse.estimatedWaitTime;
        localStorage.setItem("tokenNumer", this.tokenNum);
        localStorage.setItem("estimatedTiming", this.estimatedTime);
        this.enableContinue = false;
        if (this.cashDepositResponse) {

         this.successDialog();
          // this.cancelOperation()

          // this.snack.open(`Transaction done successfully`, 'OK', {
          //   duration: 2000,
          //   verticalPosition: 'top',
          //   horizontalPosition: 'right'
          // });
         // this.dialogService.tokenGenerationDialog();
        }

         this.cashDepositFormNew.reset();
         this.currencyImage = ''
          //this.snack.open(`Record Saved`, 'OK', {
          // duration: 2000,
          // verticalPosition: 'top',
          // horizontalPosition: 'right'
          // });

        // this.disableSave = true;
   
      }
    }, (err) => {
      this.snack.open(`Internal Server Error`, 'OK', {
        duration: 2000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }
  _keyUp(){
    if (this.cashDepositFormNew.get('accountNumber').value == "") {
     this.cashDepositFormNew.reset();
      this.accountBal = null;
      this.holderName ="";
      this.enableDenomination = false
      this.dataSource = null;
      this.cdForm.reset();
  }
  }
  fetchingHolderDetailsForCashDeposit(accountnum) {
    this.currencyImage = null
    this.getChargeDetails();
    this.loading = true;
    
    // let accountId=accountnum.target.value;
   
    this.api.fetchingDetailsForCash(accountnum).subscribe(resp => {
      this.loading = false;

      if (resp.accountStatus && resp.accountStatus !="APPROVED") {
        this.snack.open("Account Number is not Approved :" + accountnum, "", {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        this.cashDepositFormNew.reset();
       // this.ngOnInit();
        this.getCountrynameValues();
        this.getAccountBranch();
       
        return;
      }
      if (resp) {
        this.accountResp=resp;
        this.accountBal = resp.accBalance;
        this.holderName = resp.accHolderName;
        this.invalidAccNo = false;
        this.invalidAccNoMsg = resp.message
        if(this.invalidAccNoMsg == "no value present"){
         this.invalidAccNo = true;
        }
        this.cashDepositFormNew.get('holderName').patchValue(resp.accHolderName);
        this.cashDepositFormNew.get('accountCurrency').patchValue(resp.currency);
        this.cashDepositFormNew.get('transactionCurrency').patchValue(resp.currency);
        this.cashDepositFormNew.get('accountType').patchValue(resp.accountType);
        this.cashDepositFormNew.get('transactionBranch').setValue(this.branchName);
        if(resp.accountBranch != null && resp.accountBranch !='' && resp.accountBranch!=' ' && resp.accountBranch!=undefined){
          this.cashDepositFormNew.get('accountBranch').patchValue(resp.accountBranch);
        }else{
          this.branchName='Kasturi Nagar';
          this.cashDepositFormNew.get('accountBranch').setValue(this.branchName);
        }
      }
      else{
        this.invalidAccNo = true;
       
        this.snack.open("Incorrect Account Number", 'OK', {
          duration: 2000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
        
      }
    },
     (err) => {
      this.loading = false;
      this.snack.open(err.error.text, "", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.invalidAccNo = true;
      this.accountBal = null;
      this.holderName = "";
      this.cashDepositFormNew.reset();
  
    })

  }

  
  addCurrencyImage(currency) {
    return currency?.toLowerCase();
  }

  onCountryChange(country) {
    this.disableSave = this.denomRequired =='yes' ? true : false;    
    if(country.currencyCode != this.cashDepositFormNew.get('accountCurrency').value){
      this.cashDepositFormNew.get('exchangeRate').setValue(null)
      this.apiService.getExchangeRateforCurrency(country.currencyCode,this.cashDepositFormNew.get('accountCurrency').value).subscribe(res => {
        console.log(res, res[0], 'buyrate',res[0].buyRate);
  this.exchangeRate = Number(res[res.length-1].buyRate);
  this.cashDepositFormNew.get('exchangeRate').setValue(this.exchangeRate)
  // this.calculateTotalCharge();
  this.calculateTotalTransactionAmount();
      })
    }
    else{
      this.cashDepositFormNew.get('exchangeRate').setValue(null)
      this.calculateTotalCharge();
      this.calculateTotalTransactionAmount();
    }
    this.cdForm.reset();
    this.cashDepositFormNew.get('totalAmount').setValue(0);
    let currency = country.currencyCode;
    this.currencyImage = 'currency-flag-'+currency.toLowerCase();
    console.log(this.cashDepositFormNew,currency);
   
  }
  onKeyUp(event){
    this.calculateTotalTransactionAmount();
    if(this.cashDepositFormNew.get('transactionAmount').value>0){
      this.enableDenomination=true;
    }else{
      this.enableDenomination=false;
    }
  }

  getAccountBranch() {
    this.api.getAccountBranch()
      .subscribe(branches => {
       
        this.arrayAccountBranch = branches;
      })
  }


  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }

  totalAmountValue(cdForm) {
    this.a=this.cashDepositFormNew.value.transactionAmount;
    this.a=this.a.replace(/\,/g,''); // 1125, but a string, so convert it to number
    this.a=parseInt(this.a,10);


    this.cashDepositFormNew.value.totalAmount =
      ((cdForm.value.count2000 ? cdForm.value.count2000 * 2000 : 0) +
        (cdForm.value.count500 ? cdForm.value.count500 * 500 : 0) +
        (cdForm.value.count200 ? cdForm.value.count200 * 200 : 0) +
        (cdForm.value.count100 ? cdForm.value.count100 * 100 : 0) +
        (cdForm.value.count50 ? cdForm.value.count50 * 50 : 0) +
        (cdForm.value.count20 ? cdForm.value.count20 * 20 : 0) +
        (cdForm.value.count10 ? cdForm.value.count10 * 10 : 0) +
        (cdForm.value.count5 ? cdForm.value.count5 * 5 : 0) +
        (cdForm.value.count2 ? cdForm.value.count2 * 2 : 0) +
        (cdForm.value.count1 ? cdForm.value.count1 * 1 : 0)
        );
       ;
        this.transamount=this.cashDepositFormNew.value.transactionAmount;
        this.totalAmount=this.cashDepositFormNew.value.totalAmount;
     //   this.cashDepositFormNew.controls.totalAmount.setValue(this.cashDepositFormNew.value.totalAmount);
    if (this.cashDepositFormNew.value.totalAmount != this.cashDepositFormNew.value.transactionAmount) {
      this.disableSave = true
    }
    else {
      this.disableSave = false
    }

  }
  dataByCountry(index,country){
    return country.currencyCode;
  }
  dataByBranch(index,branch){
    return branch.branchName;
  }
  // getBankInformation(branchCode:any){
  //   this.apiService.fetchBankInfoByBranchCode(branchCode).subscribe(resp => {
  //     console.log(resp)
  //     this.denomRequired = resp.denomRequired
      
  //   })
  // }
  getChargeDetails() {
    this.api.getChargeDetails(this.cashDepositFormNew.value.accountNumber).subscribe((resp) => {
      this.chargeDetailArray = resp;
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
      this.calculateTotalCharge();
      if(this.cashDepositFormNew.get('trnsactionAmount').value){
        this.calculateTotalTransactionAmount();
      }
    });
  }
  waiverChange(val: boolean, i: number) {
    console.log(val);
    
    this.chargeDetailArray[i].waiver = val;
    this.calculateTotalCharge();
    this.calculateTotalTransactionAmount();
  }

  calculateTotalTransactionAmount(){
    this.transValue = 0
  if(this.cashDepositFormNew.get('exchangeRate').value){
    this.transValue = Number(this.cashDepositFormNew.get('transactionAmount').value) * Number(this.cashDepositFormNew.get('exchangeRate').value)
    this.totalTransactionAmount = (Number(this.cashDepositFormNew.get('transactionAmount').value) * Number(this.cashDepositFormNew.get('exchangeRate').value)) + Number(this.totalChargeAmount);
  }
  else{
    this.transValue = Number(this.cashDepositFormNew.get('transactionAmount').value) 
    this.totalTransactionAmount = Number(this.cashDepositFormNew.get('transactionAmount').value)  + Number(this.totalChargeAmount);
  }
  this.cashDepositFormNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount)
  console.log(this.totalTransactionAmount ,this.totalChargeAmount,this.chargeDetailArray);
  
}

calculateTotalCharge(){
  this.totalChargeAmount = 0;
  if(this.chargeDetailArray){
    for(let i = 0; i< this.chargeDetailArray.length ; i++){
      if(this.chargeDetailArray[i].waiver !=true && this.chargeDetailArray[i].amount!= null ){
        this.totalChargeAmount = this.totalChargeAmount + this.chargeDetailArray[i].amount;
      }
    }
    // if(this.cashDepositFormNew.get('exchangeRate').value){
    //   this.totalChargeAmount = this.totalChargeAmount * this.cashDepositFormNew.get('exchangeRate').value
    // }
}
this.cashDepositFormNew.get('totalChargeAmount').setValue(this.totalChargeAmount)
}
 
  
successDialog(): void {
  const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
    width: '60%',
    height: '75%',
    backdropClass: 'dialog-bg-trans',
    disableClose: true,
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    this.operationsComponent.closeDrawer();
    this.dashboard.serviceEnable=false;
    this.dashboard.getCounterDashBoard();
    this.showTransactionSlip();
    this.dashboard.getDashboardRecords();
  });
}
showTransactionSlip(){
  this.dashboard.slipEnable = true;
  this.dashboard.transactionDetails = this.cashDepositResponse
  this.dashboard.drawer.open()
}
}
