import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { FormGroup, FormBuilder, Validators, NgForm, NumberValueAccessor } from '@angular/forms';
import { OperationsComponent } from '../../operations/operations.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TokenService } from 'app/shared/services/token-service/token.service';
import { LocalStoreService } from 'app/shared/services/local-store.service';
import { DialogService } from '../../Dialogs/dialog.service';
import { ApiService } from 'app/shared/services/api.service';
import { MatTableDataSource } from '@angular/material/table';
import { DashboardComponent } from '../../dashboard/dashboard.component';
import { CustomerAuthenticatedDialogComponent } from '../../Dialogs/customer-authenticated-dialog/customer-authenticated-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { FingerprintAuthenticationComponent } from '../../Dialogs/fingerprint-authentication/fingerprint-authentication.component';
// import { threadId } from 'worker_threads';

@Component({
  selector: 'app-new-cheque-withdrwal',
  templateUrl: './new-cheque-withdrwal.component.html',
  styleUrls: ['./new-cheque-withdrwal.component.scss']
})
export class NewChequeWithdrwalComponent implements OnInit {
  @ViewChild('drawer', { static: true }) public drawer!: MatDrawer;
  @ViewChild('cdForm') cdForm: NgForm;
  loading: boolean;

  countryValues: any;
  user: any;
  accountBal: any;
  holderName: any;
  branchName:any;
  arrayAccountBranch: any;
  branchNameFromLogin: string;
  accountResp: any;
  invalidAccNo : boolean = false;
  disableSave: boolean;
  invalidAccNoMsg : any;
  trxnAmtFlag: boolean=false;
  transactionId:number;
  denomCodeArray: any[] = [
    { key: 'count2000', value: 2000 },
    { key: 'count500', value: 500 },
    { key: 'count200', value: 200 },
    { key: 'count100', value: 100 },
    { key: 'count50', value: 50 },
    { key: 'count20', value: 20 },
    { key: 'count10', value: 10 },
    { key: 'count5', value: 5 },
    { key: 'count2', value: 2 },
    { key: 'count1', value: 1 }
  ];
  a: any;
  chequeWithdrwal : any = 'Cheque Withdrawal';
  chequeWithdrwalNew: FormGroup;
  transamount: any;
  totalAmount: any;
  transAmt: any;
  currencyImage: any = "";
  chequeWithdrawalResponse: any;
  tokenNum: any;
  estimatedTime: any;
  enableContinue: boolean;
  enableDenomination:boolean=false;
  tellerId: string;
  denomination: any[];
  denomRequired: any;
  chargeDetailArray: any;
  dataSource: any;
  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];
  totalChargeAmount: number = 0;
  totalTransactionAmount: number = 0;
  exchangeRate: any;
  transValue: number;
  flex: string = "50%";
  narrativeRow: number = 2;
  validPattern = "^(?![a-zA-Z\s]+$)[a-zA-Z0-9]+$"
  constructor(private operationsComponent: OperationsComponent,
    private dialogService : DialogService,
    private snack:MatSnackBar,
    private ls:LocalStoreService,
    private api:TokenService,
    private apiService:ApiService,
    private fb: FormBuilder,
    private dashboard: DashboardComponent,
    private dialog:MatDialog) { }

    Onlynumber(event): boolean {
      const charCode = (event.which) ? event.which : event.keyCode;
      if (charCode > 31 && (charCode < 48 || charCode > 57)) {
        return false;
      }
      return true;
    }

  ngOnInit(): void {
    this.branchNameFromLogin=localStorage.getItem('branchName');
    this.tellerId = localStorage.getItem('tellerid');
    if(this.branchNameFromLogin.includes('null')){
     this.branchName='Kasturi Nagar';
    // this.arrayAccountBranch='Kasturi Nagar';
    //  this.arrayAccountBranch= [{'branchName':'Kasturi Nagar'}];
    }else{
      this.branchName=this.branchNameFromLogin;
    }
    this.denomRequired = localStorage.getItem('denomRequired');
    this.disableSave = this.denomRequired =='yes' ? true : false;    
    this.chequeWithdrwalNew = this.fb.group({
    accountNumber:['',[Validators.required,Validators.minLength(4),Validators.maxLength(15),Validators.pattern(this.validPattern)]],
    accountType:['', [Validators.required]],
    chequenumber:['', [Validators.required]],
    holderName :['', [Validators.required]], 
    accountCurrency:['', [Validators.required]],
    transactionCurrency: ['', [Validators.required]],
    transactionAmount:['', [Validators.required, Validators.min(.1)]],
    productCode:['CQW'],
    tokenOrigin:['Teller'],
    accountBranch: ['', [Validators.required]],
    customerId:[''],
    tellerId:[''],
    transactionBranch:[''],
    transactionId:[''],
    exchangeRate:[''],
    totalTransactionAmount:[''],
    totalChargeAmount:[''],
    narrative:['']

  });
  console.log("Form test:",this.chequeWithdrwalNew)
 // this.chequeWithdrwalNew.get('accountBranch').setValue(this.branchName);
 this.chequeWithdrwalNew.get('transactionBranch').setValue(this.branchName);
  this.user = this.ls.getItem('ICUST_USER');
  console.log(this.user);
  // this.chequeWithdrwalNew.controls.holderName.setValue(this.user.tellerName);
  this.getCountrynameValues();
  this.getAccountBranch();
  //this.fetchingDetailsForChequeWithdrawal();
  this.chequeWithdrwalNew.valueChanges
  .subscribe(formValue=>{
    if(formValue.transactionAmount > this.accountBal){
      this.trxnAmtFlag=true;
    }else{
      this.trxnAmtFlag=false;
    }
  })
  // this.getBankInformation('KN')
}
  cancelOperation()
  {
    this.operationsComponent.closeDrawer(); 
    this.accountBal = null;
       this.chequeWithdrwalNew.reset();
  }
  getCountrynameValues() {
    this.api.getCountryValues().subscribe((allCountryValues : any)=> {
      this.countryValues = allCountryValues;
      console.log(this.countryValues);
      });
  }

  onChequeWithdrwal()
  {
    var withoutComma=this.chequeWithdrwalNew.get('transactionAmount').value.replace(/\,/g,'');
    let denominationData: any = {};
    let denominationDataArray: any[] = [];
if(this.cdForm){
    for (let i = 0; i < this.denomCodeArray.length; i++) {
      if (this.cdForm.value[this.denomCodeArray[i].key]) {
        //denominationData.transactionId = this.cashWithdrawal1.id;
        denominationData.transactionId = this.chequeWithdrwalNew.value.transaction;
        denominationData.denominationKey = this.denomCodeArray[i].value;
        denominationData.denominationValue = +this.cdForm.value[this.denomCodeArray[i].key];
        denominationData.currency = this.chequeWithdrwalNew.value.transactionCurrency;
        // denominationData.amountAvailiable = 15200 - (cwForm.value[this.denomCodeArray[i].key] * this.denomCodeArray[i].value);
        denominationDataArray.push(denominationData);
        denominationData = {};
      }
    }

    delete this.cdForm.value.count2000;
    delete this.cdForm.value.count500;
    delete this.cdForm.value.count200;
    delete this.cdForm.value.count100;
    delete this.cdForm.value.count50;
    delete this.cdForm.value.count20;
    delete this.cdForm.value.count10;
    delete this.cdForm.value.count5;
    delete this.cdForm.value.count2;

    var denomination_Ob = [{
    //  transactionId: this.cashWithdrawal1.id,
      transactionId: this.chequeWithdrwalNew.value.transactionId,
      denominationKey:denominationData.denominationKey,
      denominationValue:denominationData.denominationValue,
      currency:this.chequeWithdrwalNew.value.transactionCurrency
   }];
   denomination_Ob=denominationDataArray;
   denominationDataArray=this.denomination;
  }
    let custId=this.accountResp.customerId;
    console.log('custId :: ',custId);
    this.chequeWithdrwalNew.get('customerId').setValue(+custId);
    this.chequeWithdrwalNew.get('tellerId').setValue(this.tellerId);
    this.chequeWithdrwalNew.get('productCode').setValue('CQW');
    this.chequeWithdrwalNew.value.denomination = denomination_Ob;
    this.chequeWithdrwalNew.value.transactionId = this.transactionId;
    this.chequeWithdrwalNew.value.transactionAmount=withoutComma;
    const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
      width: '60%',
      height: '70%',
      backdropClass: 'dialog-bg-trans',
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result != 'close'){
    this.api.saveofNewCashDeposit(this.chequeWithdrwalNew.value).subscribe(data=>{
      console.log(data);
      if(data)
      {
        this.chequeWithdrwalNew.value.transactionId = this.transactionId;
        this.transactionId = data.transactionId;
        this.accountBal = null;
        this.holderName ="";
        this.chequeWithdrawalResponse = data;
        this.chequeWithdrawalResponse.transactionType = 'Cheque Withdrawal'
        this.tokenNum = this.chequeWithdrawalResponse.tokenNumber;
        this.estimatedTime = this.chequeWithdrawalResponse.estimatedWaitTime;
        localStorage.setItem("tokenNumer", this.tokenNum);
        localStorage.setItem("estimatedTiming", this.estimatedTime);
        this.enableContinue = false;
        if (this.chequeWithdrawalResponse) {
          // this.dialogService.fingerprintDialog(null);
          // this.cancelOperation()
          //this.dialogService.tokenGenerationDialog();
          // this.snack.open(`Transaction done successfully`, 'OK', {
          //   duration: 2000,
          //   verticalPosition: 'top',
          //   horizontalPosition: 'right'
          // });
          this.successDialog();
          this.operationsComponent.closeDrawer();
          this.dashboard.getCounterDashBoard();
          this.dashboard.getDashboardRecords();
        }

       this.chequeWithdrwalNew.reset();
       this.currencyImage = null
        // this.disableSave = true;
        console.log("Disable save check",this.disableSave)
      }
    },(err)=>{
      this.snack.open(`Internal Server Error`, 'OK', {
        duration: 2000,
        verticalPosition: 'top',
        horizontalPosition: 'right'
      });
    })
  }
  })
  }
  _keyUp(){
    if (this.chequeWithdrwalNew.get('accountNumber').value == "") {
     this.chequeWithdrwalNew.reset();
      this.accountBal = null;
      this.holderName ="";
      this.enableDenomination= false;
      this.totalTransactionAmount = 0;
      this.dataSource = null;
      this.cdForm.reset();
  }
  }
  fetchingHolderDetailsForChequeWithdrawal(accountnum)
  {this.getChargeDetails();
    this.currencyImage = null;
    this.loading = true;
    console.log("vv",accountnum)

 // let accountId=accountnum.target.value;
  console.log("iii",accountnum);
    this.api.fetchingDetailsForCash(accountnum).subscribe(resp=>{
      this.loading = false;
      console.log(resp);
      if(resp){
        this.accountResp=resp;
        this.accountBal=resp.accBalance;
        this.holderName=resp.accHolderName;
        this.invalidAccNo = false;
        this.invalidAccNoMsg = resp.message;
        console.log("InavalidAccNo Test:",resp.message)//undefined
        if(this.invalidAccNoMsg == 'no value present'){
         this.invalidAccNo = true
         console.log("InavalidAccNo Test:",this.invalidAccNo)
        }
        this.chequeWithdrwalNew.get('holderName').patchValue(resp.accHolderName);
        this.chequeWithdrwalNew.get('accountCurrency').patchValue(resp.currency);
        this.chequeWithdrwalNew.get('transactionCurrency').patchValue(resp.currency);
        this.chequeWithdrwalNew.get('accountType').patchValue(resp.accountType);
        if(resp.accountBranch != null && resp.accountBranch !='' && resp.accountBranch!=' ' && resp.accountBranch!=undefined){
          this.chequeWithdrwalNew.get('accountBranch').patchValue(resp.accountBranch);
        }else{
          this.branchName='Kasturi Nagar';
          this.chequeWithdrwalNew.get('accountBranch').setValue(this.branchName);
        }
      }
      else{
        this.invalidAccNo = true;
        console.log("Invalid Account Number check: ",this.invalidAccNo)
         //this.snack.open("Incorrect Account Number", 'OK', {
         // duration: 2000,
         // verticalPosition: 'top',
         // horizontalPosition: 'right'
        //});
      }
    },(err)=>{
      console.log(err.message);
      this.loading = false;
      this.snack.open(err.error.text, "", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
      this.invalidAccNo = true;
      this.accountBal = null;
      this.holderName = "";
       this.chequeWithdrwalNew.reset();
    
    })
   
  }
  addCurrencyImage(currency) {
    return currency?.toLowerCase();
  }
  onKeyUp(event){
    this.calculateTotalTransactionAmount();
    if((this.chequeWithdrwalNew.get('transactionAmount').value <= this.accountBal)&&(this.chequeWithdrwalNew.get('transactionAmount').value>0)){
      this.enableDenomination=true;
    }else{
      this.enableDenomination=false;
    }
    
  }

  onCountryChange(country) {
    this.disableSave = this.denomRequired =='yes' ? true : false;    
    if(country.currencyCode != this.chequeWithdrwalNew.get('accountCurrency').value){
      this.flex = "100%";
      this.narrativeRow = 1;
      this.chequeWithdrwalNew.get('exchangeRate').setValue(null);
      this.apiService.getExchangeRateforCurrency(country.currencyCode,this.chequeWithdrwalNew.get('accountCurrency').value).subscribe(res => {
        this.exchangeRate = res[res.length-1].sellRate
  this.chequeWithdrwalNew.get('exchangeRate').setValue(res[res.length-1].sellRate)
  // this.calculateTotalCharge();
  this.calculateTotalTransactionAmount();
      })
    }
    else{
      this.flex="50%"
      this.narrativeRow = 2;
      this.chequeWithdrwalNew.get('exchangeRate').setValue(0);
this.calculateTotalCharge();
this.calculateTotalTransactionAmount();
    }
    this.cdForm.reset();
    this.chequeWithdrwalNew.get('totalAmount').setValue(0);
    let currency = country.currencyCode;
    this.currencyImage = 'currency-flag-'+currency.toLowerCase();
  }

  getAccountBranch() {
    this.api.getAccountBranch()
      .subscribe(branches => {
        console.log('branches :: ', branches);
        this.arrayAccountBranch = branches;
      })
  }

  _keyPress(event: any) {
    const pattern = /[0-9]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (!pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }

  totalAmountValue(cdForm) {
    this.a=this.chequeWithdrwalNew.value.transactionAmount;
    this.a=this.a.replace(/\,/g,''); // 1125, but a string, so convert it to number
    this.a=parseInt(this.a,10);


    this.chequeWithdrwalNew.value.totalAmount =
      ((cdForm.value.count2000 ? cdForm.value.count2000 * 2000 : 0) +
        (cdForm.value.count500 ? cdForm.value.count500 * 500 : 0) +
        (cdForm.value.count200 ? cdForm.value.count200 * 200 : 0) +
        (cdForm.value.count100 ? cdForm.value.count100 * 100 : 0) +
        (cdForm.value.count50 ? cdForm.value.count50 * 50 : 0) +
        (cdForm.value.count20 ? cdForm.value.count20 * 20 : 0) +
        (cdForm.value.count10 ? cdForm.value.count10 * 10 : 0) +
        (cdForm.value.count5 ? cdForm.value.count5 * 5 : 0) +
        (cdForm.value.count2 ? cdForm.value.count2 * 2 : 0) +
        (cdForm.value.count1 ? cdForm.value.count1 * 1 : 0)
        );
        console.log("Total Amount",this.chequeWithdrwalNew.value.totalAmount)
        console.log("Transaction Amount",this.chequeWithdrwalNew.value.transactionAmount);
        this.transamount=this.chequeWithdrwalNew.value.transactionAmount;
        this.totalAmount=this.chequeWithdrwalNew.value.totalAmount;
        if(this.chequeWithdrwalNew.value.totalAmount != this.chequeWithdrwalNew.value.transactionAmount){
          this.disableSave = true;
        }
        else{
          this.disableSave = false;
        }
        
       // this.chequeWithdrwalNew.controls.totalAmount.setValue(this.chequeWithdrwalNew.value.totalAmount);
       console.log("Test:",this.chequeWithdrwalNew.controls)
       console.log("Form Check:::",this.chequeWithdrwalNew)
        
  }
  // getBankInformation(branchCode:any){
  //   this.apiService.fetchBankInfoByBranchCode(branchCode).subscribe(resp => {
  //     console.log(resp)
  //     this.denomRequired = resp.denomRequired
      
  //   })
  // }
  getChargeDetails() {
    this.api.getChargeDetails(this.chequeWithdrwalNew.value.accountNumber).subscribe((resp) => {
      this.chargeDetailArray = resp;
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
      this.calculateTotalCharge();
      if(this.chequeWithdrwalNew.get('transactionAmount').value){
         this.calculateTotalTransactionAmount();
      }
     
    });
  }
  waiverChange(val: boolean, i: number) {
    console.log(val);
    
    this.chargeDetailArray[i].waiver = val;
    this.calculateTotalCharge();
    this.calculateTotalTransactionAmount();
  }

  calculateTotalTransactionAmount(){
    this.totalTransactionAmount = 0
    this.transValue = 0
  if(this.chequeWithdrwalNew.get('exchangeRate').value){
    this.transValue = Number(this.chequeWithdrwalNew.get('transactionAmount').value)*Number(this.chequeWithdrwalNew.get('exchangeRate').value)
    this.totalTransactionAmount = (Number(this.chequeWithdrwalNew.get('transactionAmount').value)*Number(this.chequeWithdrwalNew.get('exchangeRate').value)) + Number(this.totalChargeAmount);
    this.chequeWithdrwalNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount);
  }
  else{
    this.transValue = Number(this.chequeWithdrwalNew.get('transactionAmount').value)
    this.totalTransactionAmount = Number(this.chequeWithdrwalNew.get('transactionAmount').value) + Number(this.totalChargeAmount);
    this.chequeWithdrwalNew.get('totalTransactionAmount').setValue(this.totalTransactionAmount)
  }
 
  console.log(this.totalTransactionAmount ,this.totalChargeAmount,this.chargeDetailArray);
  
}

calculateTotalCharge(){
  this.totalChargeAmount = 0;
  if(this.chargeDetailArray){
    for(let i = 0; i< this.chargeDetailArray.length ; i++){
      if(this.chargeDetailArray[i].waiver !=true && this.chargeDetailArray[i].amount!= null ){
        this.totalChargeAmount = this.totalChargeAmount + this.chargeDetailArray[i].amount;
      }
    }
    // if(this.chequeWithdrwalNew.get('exchangeRate').value){
    //   this.totalChargeAmount = this.totalChargeAmount * this.chequeWithdrwalNew.get('exchangeRate').value
    // }
}
this.chequeWithdrwalNew.get('totalChargeAmount').setValue(this.totalChargeAmount)
}
successDialog(): void {
  const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
    width: '60%',
    height: '75%',
    backdropClass: 'dialog-bg-trans',
  });

  dialogRef.afterClosed().subscribe(result => {
    console.log('The dialog was closed');
    this.showTransactionSlip();
  });
}
 showTransactionSlip() {
    this.dashboard.slipEnable = true;
    this.dashboard.transactionDetails = this.chequeWithdrawalResponse
    this.dashboard.drawer.open()
  }
}
