import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatAccordion } from '@angular/material/expansion';
import { MatTableDataSource } from '@angular/material/table';
import { LoanPayment } from 'app/shared/models/loanPayment';

import { TokenService } from 'app/shared/services/token-service/token.service';
import { forkJoin } from 'rxjs';
import { OperationsComponent } from '../../operations/operations.component';
import { FingerPrintVerificationComponent } from '../finger-print-verification/finger-print-verification.component';

@Component({
  selector: 'app-loan-payment',
  templateUrl: './loan-payment.component.html',
  styleUrls: ['./loan-payment.component.scss']
})
export class LoanPaymentComponent implements OnInit {

  loanPayment:LoanPayment;
  @Input("loanPaymentData") loanPaymentData: any;
  @ViewChild(MatAccordion) accordion: MatAccordion;


  title: string;
  userName: string = "Mitchell Williamson";
  operation: string;
  detailsPanelOpened: boolean = false;
  chargeDetailsPanelOpened: boolean = false;
  denominationPanelOpened: boolean = false;
  chargeDetailArray : any[] = [];
  dataSource : any;
  accountTypeId : number;
  pauseTrans: boolean=false;

  @ViewChild('cdForm') cdForm: NgForm;

  totalAmt: Number;

  displayedColumns: string[] = ['waiver', 'intrestType', 'accountId', 'amount'];

  constructor( private tokenService: TokenService,
    private operationsComponent : OperationsComponent,
    public dialog: MatDialog) { }

    ngOnChanges() {
      this.loanPayment = this.loanPaymentData;
      this.loanPayment.totalAmount = 0;
    }

  ngOnInit(): void {
    this.loanPayment = this.loanPaymentData;
    this.loanPayment.totalAmount = 0;
    this.getChargeDetails();
  }
  pauseTransaction(){
    this.pauseTrans = !this.pauseTrans;
  }
  getChargeDetails(){
    if(this.loanPayment.accountType == "Savings"){
      this.accountTypeId = 1;
    }
    else if(this.loanPayment.accountType == "Current"){
      this.accountTypeId = 2;
    }
    else if(this.loanPayment.accountType == "Joint"){
      this.accountTypeId = 3;
    }
    else{
      this.accountTypeId = null;
    }
    this.tokenService.getChargeDetails(this.loanPayment.accountNumber).subscribe((resp) => {
      this.chargeDetailArray = resp;
      this.dataSource = new MatTableDataSource(this.chargeDetailArray);
    });
  }

  waiverChange(val: boolean, i: number) {
    this.chargeDetailArray[i].waiver = val;
  }

  onclickofConfirm(ftForm: NgForm) {

    this.loanPayment = ftForm.value;
    if(this.pauseTrans==false){
    forkJoin(
      this.tokenService.saveLoanpayment(this.loanPayment),
      this.tokenService.saveChargeDetails(this.chargeDetailArray)
    ).subscribe((res) => {
      if (res[0] && res[1]) {
        this.cdForm.reset();
        this.accordion.closeAll();
        this.operationsComponent.closeDrawerAfterSuccess();
      }
    },
      (error) => {
      }
    );
    }

  }

  omit_number(event) {
    var key;
    key = event.charCode;
    return ((key > 47 && key < 58));
  }
  cancelOperation(){
    this.cdForm.reset();
    this.accordion.closeAll();
    this.operationsComponent.closeDrawer();
  }
  openDialog(): void {
    var name='cat';
     let dialogRef = this.dialog.open(FingerPrintVerificationComponent, {
       width: '500px',
       height:'500px',
       data: { name: name }
     });
 
     dialogRef.afterClosed().subscribe(result => {
       name = result;
     });
   }

}
