import { MatDialog } from '@angular/material/dialog';
import { Inject, Injectable,Input } from '@angular/core';
import { CustomerAuthenticatedDialogComponent } from './customer-authenticated-dialog/customer-authenticated-dialog.component';
import { FingerprintAuthenticationComponent } from './fingerprint-authentication/fingerprint-authentication.component';
import { Router } from '@angular/router';
import { DOCUMENT } from '@angular/common';
import { TransactionDetailComponent } from './transaction-detail/transaction-detail.component';
import { WithdrawlAlertDialogueComponent } from '../tokenOperations/withdrawl-alert-dialogue/withdrawl-alert-dialogue.component';
import { TokenGenerationComponent } from '../tokenOperations/token-generation/token-generation.component';
import { DashboardComponent } from '../dashboard/dashboard.component';

export class TokenObjects{
  entityCode:string;
  bankCode:string;
  tokenBranch:string;
  accountNumber:number
  customerCategory:string;
  productCode:string;

}

@Injectable({
  providedIn: 'root'
})

export class DialogService {
  @Input() tokenResp:any;
  tokenObjects = new TokenObjects();
  constructor(
    public dialog: MatDialog,
    private router: Router,
    @Inject(DOCUMENT) private _document: Document
  ) { }

  transactionDetailsDialog(tokenId,screen,enableBtn,item): void {
    const dialogRef = this.dialog.open(TransactionDetailComponent, {
      width: '75%',
      height: '90%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
      data: {
        dataKey: tokenId,
        screen:screen,
        enableBtn:enableBtn,
        data:item
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  successDialog(): void {
    const dialogRef = this.dialog.open(CustomerAuthenticatedDialogComponent, {
      width: '60%',
      height: '75%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
  
    });
  }

  fingerprintDialog(customerId): void {
    const dialogRef = this.dialog.open(FingerprintAuthenticationComponent, {
      width: '60%',
      height: '70%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      this.successDialog();
    });
  }

  tokenGenerationDialog(){
    console.log(this.tokenResp ,"Token Response");
    this.tokenObjects.entityCode='entityCode1';
     const tokenDialog = this.dialog.open(TokenGenerationComponent, {
       data:{},
        width: '55%',
      height: '72%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
    });
  }
  withdrawlAlertDialogue(tokenNumber) {
    console.log("inside alertDialog ::",tokenNumber)
    let data = {
      "tokenNumber":tokenNumber
    };
    const dialogRef = this.dialog.open(WithdrawlAlertDialogueComponent, {
      width: '40%',
      backdropClass: 'dialog-bg-trans',
      disableClose: true,
      data: { payload: data }
    });

    return dialogRef.afterClosed();

  }


}
